import SearchManager from 'splunkjs/mvc/searchmanager';
import _ from 'lodash';
import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { app } from '@splunk/splunk-utils/config';

/**
 * Model for Datamanager
 */
class DataManagerModel extends Component {
  static propTypes = {
    onStateChange: PropTypes.func,
    id: PropTypes.string,
    statusBuckets: PropTypes.number,
    returnCount: PropTypes.number,
    returnOffset: PropTypes.number,
  }

  static defaultProps = {
    onStateChange: () => {},
    id: '',
    statusBuckets: 0,
    returnCount: 0,
    returnOffset: 0,
  }

  constructor(props, context) {
    super(props, context);
    this.state = {
      fields: [],
      data: [],
      isJobDone: false,
      searchTiming: { start: null, end: null },
    };
    this.createSearchManager();
    this.onDataChange = this.onDataChange.bind(this);
    this.onSearchStart = this.onSearchStart.bind(this);
    this.onSearchProgress = this.onSearchProgress.bind(this);
  }

  componentDidMount() {
    this.registerEvents();
  }

  shouldComponentUpdate(nextProps) {
    return nextProps.search !== this.props.search;
  }

  componentDidUpdate() {
    this.createSearchManager();
    // Reset
    this.unregisterEvents();
    this.registerEvents();
  }

  componentWillUnmount() {
    this.unregisterEvents();
  }

  /**
   * Callback for search manager's start event
   * @private
   */
  onSearchStart() {
    this.state.searchTiming.start = Date.now();
    this.state.isJobDone = false;
  }

  /**
   * Callback for search manager's search in progress event
   * @param {Object} state - state of the search
   * @private
   */
  onSearchProgress(state) {
    const content = state.content;
    this.state.isJobDone = content.isDone || false;
  }

  /**
   * Callback for when the search manager returns some data
   * @private
   */
  onDataChange() {
    const data = this.resultsModel.data();
    const fields = (data && data.fields) || [];
    const rows = (data && data.rows) || [];
    this.setState({
      fields,
      data: rows,
    });
    if (this.props.onStateChange) {
      this.props.onStateChange(this.state);
    }
  }

  registerEvents() {
    this.searchManager.on('search:start', this.onSearchStart, this);
    this.searchManager.on('search:progress', this.onSearchProgress, this);
    this.resultsModel.on('change', this.onDataChange, this);
    this.resultsModel.on('data', this.onDataChange, this);
  }

  unregisterEvents() {
    this.searchManager.off('search');
    this.resultsModel.off('change');
  }

  createSearchManager() {
    this.id = this.props.id || _.uniqueId();
    this.searchManager = new SearchManager({
      id: `${this.id}_manager`,
      earliest_time: this.props.earliest_time,
      latest_time: this.props.latest_time,
      preview: false,
      cache: 60,
      status_buckets: this.props.statusBuckets || 0,
      search: this.props.search,
      time_format: '%s.%Q',
      app,
    });
    this.resultsModel = this.searchManager.data('preview', {
      output_mode: 'json_rows',
      count: this.props.returnCount || 0,
      offset: this.props.returnOffset || 0,
      output_time_format: '%s.%Q',
    });
  }

  render() {
    return (<p />);
  }
}

DataManagerModel.defaultProps = {
  earliest_time: '-60m',
  latest_time: 'now',
  search: 'index=_internal ERROR | timechart count',
};

DataManagerModel.propTypes = {
  earliest_time: PropTypes.string,
  latest_time: PropTypes.string,
  search: PropTypes.string.isRequired,
};
export default DataManagerModel;



