import Tooltip from '@splunk/react-ui/Tooltip';
import Clipboard from '@splunk/react-icons/Clipboard';
import { _ } from '@splunk/ui-utils/i18n';
import React from 'react';
import PropTypes from 'prop-types';
import { createTestHook } from '../../util/TestSupport';

export default class CopyableConfigText extends React.Component {
  static propTypes = {
    children: PropTypes.any,
  };

  static defaultProps = {
    children: null,
  };

  constructor() {
    super();
    this.state = {
      tooltipText: this.defaultTooltipText,
      open: false,
    };

    this.handleClick = this.handleClick.bind(this);
    this.onTooltipOpen = this.onTooltipOpen.bind(this);
    this.onTooltipClose = this.onTooltipClose.bind(this);
  }

  onTooltipOpen() {
    this.setState({
      tooltipText: this.defaultTooltipText,
      open: true,
    });
  }

  onTooltipClose() {
    this.setState({
      open: false,
    });
  }

  handleClick(e) {
    e.preventDefault();
    this.setState({ tooltipText: this.copiedTooltipText });
    const input = document.createElement('input');
    const command = this.props.children.join('');
    // Get the text from the element passed into the input
    input.setAttribute('value', command);
    // Append the input to the body
    document.body.appendChild(input);
    input.select();
    // Execute the copy command
    document.execCommand('copy');
    // Remove the input from the body
    document.body.removeChild(input);
    return false;
  }

  styles = {
    clipboard: {
      verticalAlign: 'top',
      width: 15,
      height: 15,
      border: '1px solid #ccc',
      borderRight: '0px',
      padding: '6px',
      borderRadius: '5px 0px 0px 5px',
      cursor: 'pointer',
    },
    code: {
      width: '80%',
      backgroundColor: '#eeeeee',
      padding: 5,
      border: '1px solid #ccc',
      borderRadius: '0px 5px 5px 0px',
      fontFamily: 'Splunk Platform Mono,Droid Sans Mono,Consolas,Monaco,Courier New,Courier,monospace',
      fontSize: '11px',
      color: 'dimgray',
      whiteSpace: 'pre-wrap',
      overflow: 'auto',
    },
  };

  defaultTooltipText = _('Copy to clipboard');
  copiedTooltipText = _('Copied!');

  render() {
    return (
      <div style={{ display: 'inline-flex' }} {...createTestHook(__filename, name)}>
        <Tooltip
          content={this.state.tooltipText}
          onRequestOpen={this.onTooltipOpen}
          onRequestClose={this.onTooltipClose}
          open={this.state.open}
        >
          <Clipboard
            style={this.styles.clipboard}
            onClick={this.handleClick}
          />
        </Tooltip>
        <code style={this.styles.code}>
          {this.props.children}
        </code>
      </div>
    );
  }
}



