import React from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import pick from 'lodash/pick';
import ConfigurePageContents from '../../components/configure/ConfigurePageContents';
import {
  updateSelectedDimensions,
} from '../../actions/common/DimensionMultiSelect';
import {
  handleCount,
  enableHEC,
  updateCustomMetricsAndLogs,
} from '../../actions/configure/ConfigurePageContents';
import {
  onLoadAllSourceTypes,
} from '../../actions/configure/unixConfiguration/SourceTypesRetrieve';

/**
 * Container for ConfigurePageContents. This needs to be ALWAYS kept in sync with
 * ConfigurePageContents. A very inelegant solution but this is the only way we
 * can get sagas to trigger on component mount.
 *
 * @class ConfigurePageContainer
 * @extends {React.Component}
 */
class ConfigurePageContainer extends React.Component {
  static propTypes = { ...ConfigurePageContents.propTypes }

  static defaultProps = { ...ConfigurePageContents.defaultProps }

  constructor(props) {
    super(props);
    this.state = {
      displayHostName: window.location.hostname,
      hostName: window.location.hostname,
      hecPort: this.props.hecPort,
    };
  }

  componentDidMount() {
    if (!this.props.isHttpInputEnabled || this.props.isFetchingToken) {
      this.props.enableHEC();
    }
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.isHttpInputEnabled && !this.state.hecPort) {
      this.setState({
        hecPort: nextProps.hecPort,
      });
    }
  }

  updateSplunkHostName = (displayHostName) => {
    const values = displayHostName.split(':');
    const host = values[0];
    const port = values[1];
    this.setState({
      displayHostName,
      hecPort: port || this.props.hecPort,
      hostName: host,
    });
  }

  render() {
    const props = {
      ...pick(this.props, Object.keys(ConfigurePageContents.propTypes)),
      ...{
        displayHostName: this.state.displayHostName,
        hostName: this.state.hostName,
        hecPort: this.state.hecPort,
        updateSplunkHostName: this.updateSplunkHostName,
        loadAllSourceTypes: this.props.onLoadAllSourceTypes,
      },
    };
    return (
      <ConfigurePageContents
        {...props}
      />
    );
  }
}

const mapStateToProps = state => ({
  // Configuration Status
  connectedCount: state.configurationStatusReducer.connectedCount,
  newCount: state.configurationStatusReducer.newCount,
  initialFetchDone: state.configurationStatusReducer.initialFetchDone,
  // Dimension Multiselect
  selectedDimensions: state.dimensionMultiselectReducer.selectedDimensions,
  // Unix Configuration
  isFetchingToken: state.unixConfigurationReducer.isFetchingToken,
  isHttpInputEnabled: state.unixConfigurationReducer.isHttpInputEnabled,
  hecToken: state.unixConfigurationReducer.hecToken,
  hecPort: state.unixConfigurationReducer.hecPort,
  errorStatusText: state.unixConfigurationReducer.errorStatusText,
  packageLink: state.unixConfigurationReducer.packageLink,
  sourceTypes: state.unixConfigurationReducer.sourceTypes,
  selectedMetrics: state.unixConfigurationReducer.selectedMetrics,
  selectedMetricOptions: state.unixConfigurationReducer.selectedMetricOptions,
  selectedLogs: state.unixConfigurationReducer.selectedLogs,
});

const mapDispatchToProps = dispatch => bindActionCreators({
  onCustomDimensionChange: updateSelectedDimensions,
  onLoadAllSourceTypes,
  enableHEC,
  handleCount,
  updateCustomMetricsAndLogs,
}, dispatch);

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(ConfigurePageContainer);



