import {
  AWS_ACCOUNT_RETRIEVE,
  START,
  SUCCESS,
  FAILURE,
} from '../../actionTypes';
import ActionUtils from '../../actionUtils';

// This part will be used for saga
export const AWS_ACCOUNT_RETRIEVE_TRACKER =
  ActionUtils.createRequestTypes(AWS_ACCOUNT_RETRIEVE);

export const awsAccountInfoRetrieveTracker = {
  start: () => ActionUtils.createAction(AWS_ACCOUNT_RETRIEVE_TRACKER[START], {}),
  success: response => ActionUtils.createAction(AWS_ACCOUNT_RETRIEVE_TRACKER[SUCCESS],
    { response }),
  failure: error => ActionUtils.createAction(AWS_ACCOUNT_RETRIEVE_TRACKER[FAILURE],
    { error }),
};

export const loadAwsAccountInfo = () => ({
  type: AWS_ACCOUNT_RETRIEVE,
});



