import { createStore, applyMiddleware, combineReducers } from 'redux';
import createSagaMiddleware, { END } from 'redux-saga';
import {
  configurationStatusReducer,
  unixConfigurationReducer,
  awsConfigurationReducer,
} from '../reducers/configure/ConfigurePageContents';
import dimensionMultiselectReducer from '../reducers/common/DimensionMultiSelect';
import { addReduxLogger } from './util';

export default function entityStore(initialState) {
  const sagaMiddleware = createSagaMiddleware();
  const middleware = addReduxLogger([sagaMiddleware]);
  const store = createStore(
    combineReducers({
      configurationStatusReducer,
      unixConfigurationReducer,
      awsConfigurationReducer,
      dimensionMultiselectReducer,
    }),
    initialState,
    applyMiddleware(...middleware),
  );

  store.runSaga = sagaMiddleware.run;
  store.close = () => store.dispatch(END);
  return store;
}



