import {
  AWS_ACCOUNT_UPDATE,
  START,
  SUCCESS,
  FAILURE,
} from '../../actionTypes';
import ActionUtils from '../../actionUtils';

// This part will be used for saga
export const AWS_ACCOUNT_UPDATE_TRACKER =
  ActionUtils.createRequestTypes(AWS_ACCOUNT_UPDATE);

export const awsUpdateAccountTracker = {
  start: () => ActionUtils.createAction(AWS_ACCOUNT_UPDATE_TRACKER[START], {}),
  success: response => ActionUtils.createAction(AWS_ACCOUNT_UPDATE_TRACKER[SUCCESS],
    { response }),
  failure: error => ActionUtils.createAction(AWS_ACCOUNT_UPDATE_TRACKER[FAILURE],
    { error }),
};

/**
 * @param  {string} name  - The aws account name
 * @param  {string} keyId
 * @param  {string} secretKey
 * @param  {int} regionCategory - The aws region
 * @return {Object} - an action object with the results of the request
 */
export function onUpdateAwsAccount(name, keyId, secretKey, regionCategory) {
  return {
    type: AWS_ACCOUNT_UPDATE,
    name,
    key_id: keyId,
    secret_key: secretKey,
    category: regionCategory,
  };
}



