import {
  CLOUDWATCH_INPUT_CREATE,
  CLOUDWATCH_REGIONS_RETRIEVE,
  START,
  SUCCESS,
  FAILURE,
} from '../../actionTypes';
import ActionUtils from '../../actionUtils';

// This part will be used for saga
export const CLOUDWATCH_INPUT_CREATION_TRACKER =
  ActionUtils.createRequestTypes(CLOUDWATCH_INPUT_CREATE);

export const addNewCloudWatchInput = {
  start: () => ActionUtils.createAction(CLOUDWATCH_INPUT_CREATION_TRACKER[START], {}),
  success: response => ActionUtils.createAction(CLOUDWATCH_INPUT_CREATION_TRACKER[SUCCESS],
    { response }),
  failure: error => ActionUtils.createAction(CLOUDWATCH_INPUT_CREATION_TRACKER[FAILURE],
    { error }),
};

export const createCloudWatchInput =
  (name, accountName, awsRegions, selectedNamespaces, customDimensions) => ({
    type: CLOUDWATCH_INPUT_CREATE,
    name,
    accountName,
    awsRegions,
    selectedNamespaces,
    customDimensions,
  });

// This part will be used for saga
export const CLOUDWATCH_REGIONS_RETRIEVE_TRACKER =
  ActionUtils.createRequestTypes(CLOUDWATCH_REGIONS_RETRIEVE);

export const cloudWatchRegionsRetrieveTracker = {
  start: () => ActionUtils.createAction(CLOUDWATCH_REGIONS_RETRIEVE_TRACKER[START], {}),
  success: response => ActionUtils.createAction(CLOUDWATCH_REGIONS_RETRIEVE_TRACKER[SUCCESS],
    { response }),
  failure: error => ActionUtils.createAction(CLOUDWATCH_REGIONS_RETRIEVE_TRACKER[FAILURE],
    { error }),
};

export const loadCloudWatchRegions = awsAccount => ({
  type: CLOUDWATCH_REGIONS_RETRIEVE,
  awsAccount,
});



