import React, { Component } from 'react';
import { _ } from '@splunk/ui-utils/i18n';
import lodash from 'lodash';
import PropTypes from 'prop-types';
import Button from '@splunk/react-ui/Button';
import Modal from '@splunk/react-ui/Modal';
import ColumnLayout from '@splunk/react-ui/ColumnLayout';
import Switch from '@splunk/react-ui/Switch';
import Message from '@splunk/react-ui/Message';
import Link from '@splunk/react-ui/Link';
import Text from '@splunk/react-ui/Text';
import Select from '@splunk/react-ui/Select';
import Remove from '@splunk/react-icons/Remove';

import { CPU } from '../../util/StateConstants';
import { pluralizeNoun } from '../../libs/EMUtils';

class CustomizeInputsModal extends Component {
  static propTypes = {
    open: PropTypes.bool,
    handleRequestClose: PropTypes.func.isRequired,
    updateCustomMetricsAndLogs: PropTypes.func.isRequired,
    sourceTypes: PropTypes.arrayOf(PropTypes.string),
    selectedMetrics: PropTypes.array.isRequired,
    selectedMetricOptions: PropTypes.array.isRequired,
    metricOptionLabels: PropTypes.object.isRequired,
    selectedLogs: PropTypes.object.isRequired,
  };

  static defaultProps = {
    open: false,
    sourceTypes: [],
  };

  constructor(props, context) {
    super(props, context);
    this.selectedMetrics = this.props.selectedMetrics;
    this.selectedMetricOptions = this.props.selectedMetricOptions;
    this.metricOptionLabels = this.props.metricOptionLabels;
    this.defaultLogs = this.props.selectedLogs;
    this.state = {
      customLogs: [],
      selectedMetrics: this.selectedMetrics,
      selectedMetricOptions: this.selectedMetricOptions,
      selectedLogs: lodash.keys(this.defaultLogs),
      saveBtnEnabled: true,
      showMetricsError: false,
    };
    this.savedState = this.state;
    this.styles = {
      removeLog: {
        color: '#1e93c6',
        fontSize: '14px',
        cursor: 'pointer',
      },
      customLogInput: {
        marginTop: '10px',
      },
      addNewSource: {
        marginTop: '10px',
      },
    };
    this.submit = this.submit.bind(this);
    this.cancelAndCloseModal = this.cancelAndCloseModal.bind(this);
  }

  checkAllCustomLogsValidity = () => {
    let retVal = true;
    if (this.state.customLogs.length) {
      retVal = lodash.reduce(
        this.state.customLogs, (reducedBool, log) => reducedBool && log.isValidLog, true);
    }
    this.setState({ saveBtnEnabled: retVal });
  }

  displayCustomLogs = () => lodash.map(this.state.customLogs, log =>
    (<ColumnLayout key={log.key} style={this.styles.customLogInput}>
      <ColumnLayout.Row>
        <ColumnLayout.Column span={12} style={{ marginRight: '10px' }}>
          <Text
            error={!log.isValidLog && log.path !== ''}
            canClear
            value={log.path}
            onChange={(e, v) => this.saveCustomLogPath(v.value, log.key)}
            placeholder="Enter a valid log source"
          />
        </ColumnLayout.Column>
        <ColumnLayout.Column span={12} style={{ marginLeft: '0px' }}>
          <Select
            value={log.sourceType || ''}
            onChange={(e, v) => this.saveCustomLogSourcetype(v.value, log.key)}
            prefixLabel="Sourcetype"
          >
            {/* Value for option Automatic is intentionally blank */}
            <Select.Option key="Automatic" label="Automatic" value="" />
            {this.props.sourceTypes.map(option =>
              <Select.Option key={`${option}`} label={`${option}`} value={`${option}`} />,
            )}
          </Select>
        </ColumnLayout.Column>
        <ColumnLayout.Column span={1} style={{ marginLeft: '0px', marginRight: '15px' }}>
          <Remove
            style={this.styles.removeLog}
            onClick={e => this.removeCustomLog(log.key, e)}
          />
        </ColumnLayout.Column>
      </ColumnLayout.Row>
    </ColumnLayout>),
  );

  addNewCustomLog = () => {
    const customLogId = lodash.uniqueId('customLog-');
    const oldCustomLogs = this.state.customLogs;
    const newCustomLog = {
      key: customLogId,
      path: '',
      sourceType: '',
      isValidLog: false,
    };
    this.setState({
      customLogs: [...oldCustomLogs, newCustomLog],
    }, this.checkAllCustomLogsValidity);
  }

  removeCustomLog = (customLogId) => {
    this.setState({
      customLogs: this.state.customLogs.filter(e => e.key !== customLogId),
    }, this.checkAllCustomLogsValidity);
  }

  isValidUnixPath = (path) => {
    // this regex allows for relative paths. Change this if requirements change
    const regExp = '[^a-zA-Z0-9_.-/]+';
    return !!((path.length > 0 && path.match(regExp) === null));
  }

  saveCustomLogPath = (value, logId) => {
    const customLogs = lodash.clone(this.state.customLogs);
    const targetLog = lodash.find(customLogs, log => log.key === logId);
    targetLog.path = value;
    targetLog.isValidLog = this.isValidUnixPath(value);
    this.setState({
      customLogs,
    }, this.checkAllCustomLogsValidity);
  };

  saveCustomLogSourcetype = (value, logId) => {
    const customLogs = lodash.clone(this.state.customLogs);
    const targetLog = lodash.find(customLogs, log => log.key === logId);
    targetLog.sourceType = value;
    this.setState({
      customLogs,
    });
  };

  handleMetricsClick = (e, { value }) => {
    const prevMetricsArr = this.state.selectedMetrics;
    this.hideMetricsError();
    if (lodash.includes(prevMetricsArr, value)) {
      this.setState({
        selectedMetrics: lodash.without(prevMetricsArr, value),
      });
    } else {
      this.setState({
        selectedMetrics: [...prevMetricsArr, value],
      });
    }
  };

  handleMetricOptionsClick = (e, { value }) => {
    const prevMetricOptionsArr = this.state.selectedMetricOptions;
    if (lodash.includes(prevMetricOptionsArr, value)) {
      this.setState({
        selectedMetricOptions: lodash.without(prevMetricOptionsArr, value),
      });
    } else {
      this.setState({
        selectedMetricOptions: [...prevMetricOptionsArr, value],
      });
    }
  };

  handleLogClick = (e, { value }) => {
    const previousSelectedLogs = this.state.selectedLogs;
    if (lodash.includes(previousSelectedLogs, value)) {
      this.setState({
        selectedLogs: lodash.without(previousSelectedLogs, value),
      });
    } else {
      this.setState({
        selectedLogs: [...previousSelectedLogs, value],
      });
    }
  };

  showMetricsError = () => {
    this.setState({
      showMetricsError: true,
    });
  }

  hideMetricsError = () => {
    this.setState({
      showMetricsError: false,
    });
  }

  cancelAndCloseModal() {
    this.setState({ ...this.savedState });
    this.props.handleRequestClose();
  }

  submit() {
    const selectedLogsWithSources = lodash.pick(this.defaultLogs, this.state.selectedLogs);

    if (this.state.selectedMetrics.length > 0) {
      this.props.updateCustomMetricsAndLogs({
        selectedMetrics: this.state.selectedMetrics,
        selectedMetricOptions: this.state.selectedMetricOptions,
        selectedLogs: selectedLogsWithSources,
        customLogs: this.state.customLogs,
      });
      this.savedState = this.state;
      this.props.handleRequestClose();
    } else {
      this.showMetricsError();
    }
  }

  selectAllMetrics = () => {
    this.setState({
      selectedMetrics: this.selectedMetrics,
    });
    this.hideMetricsError();
  };

  deselectAllMetrics = () => {
    this.setState({
      selectedMetrics: [CPU],
    });
  };

  selectAllLogs = () => {
    this.setState({
      selectedLogs: lodash.keys(this.defaultLogs),
    });
  };

  deselectAllLogs = () => {
    this.setState({
      selectedLogs: [],
    });
  };

  render() {
    const colStyle = { padding: 10 };
    const metrics = this.selectedMetrics;
    const metricOptions = this.selectedMetricOptions;
    const logSources = lodash.keys(this.defaultLogs);
    const selectedMetrics = this.state.selectedMetrics;
    const selectedMetricOptions = this.state.selectedMetricOptions;
    const selectedLogs = this.state.selectedLogs;

    const metricsSwitches = metrics.map((value) => {
      // CPU is always disabled by default so that it cannot be de-selected
      const isDisabled = value === CPU;
      return (
        <Switch
          key={value}
          value={value}
          selected={lodash.includes(selectedMetrics, value)}
          disabled={isDisabled}
          onClick={this.handleMetricsClick}
          appearance="checkbox"
          size="small"
        >
          {value}
        </Switch>
      );
    });

    const metricOptionsSwitches = metricOptions.map((value) => {
      const isDisabled = false;
      const label = value in this.metricOptionLabels ? this.metricOptionLabels[value] : value;
      return (
        <Switch
          key={value}
          value={value}
          selected={lodash.includes(selectedMetricOptions, value)}
          disabled={isDisabled}
          onClick={this.handleMetricOptionsClick}
          appearance="checkbox"
          size="small"
        >
          {label}
        </Switch>
      );
    });

    const logSourcesSwitches = logSources.map(value => (
      <Switch
        key={value}
        value={value}
        selected={lodash.includes(selectedLogs, value)}
        onClick={this.handleLogClick}
        appearance="checkbox"
        size="small"
      >
        {value}
      </Switch>
    ));

    const metricSelectedPhrase = `${pluralizeNoun(this.state.selectedMetrics.length, 'Metric')} Selected`;
    const logSourceSelectedPhrase = `${pluralizeNoun(this.state.selectedLogs.length, 'Log Source')} Selected`;

    return (
      <div>
        <Modal
          onRequestClose={this.cancelAndCloseModal}
          open={this.props.open}
          style={{ minWidth: '700px' }}
          id="customize-data-collection-modal"
        >
          <Modal.Header title={_('Customize Data Collection')} onRequestClose={this.cancelAndCloseModal} />
          <Modal.Body style={{ padding: '0' }}>
            <ColumnLayout divider="vertical" gutter={0} style={{ paddingLeft: '10px' }}>
              <ColumnLayout.Row >
                <ColumnLayout.Column span={4} style={colStyle}>
                  <p>{this.state.selectedMetrics.length} {metricSelectedPhrase}</p>
                  <ColumnLayout.Row>
                    <ColumnLayout.Column span={6}>
                      <Link
                        id="select-all-metrics"
                        onClick={this.selectAllMetrics}
                      >
                        {_('Select All')}
                      </Link>
                    </ColumnLayout.Column>
                    <ColumnLayout.Column span={6}>
                      <Link
                        id="deselect-all-metrics"
                        onClick={this.deselectAllMetrics}
                      >
                        {_('Deselect All')}
                      </Link>
                    </ColumnLayout.Column>
                  </ColumnLayout.Row>
                </ColumnLayout.Column>

                <ColumnLayout.Column span={8} style={colStyle}>
                  <p>{this.state.selectedLogs.length} {logSourceSelectedPhrase}</p>
                  <ColumnLayout.Row>
                    <ColumnLayout.Column span={3}>
                      <Link
                        id="select-all-logs"
                        onClick={this.selectAllLogs}
                      >
                        {_('Select All')}
                      </Link>
                    </ColumnLayout.Column>
                    <ColumnLayout.Column span={9}>
                      <Link
                        id="deselect-all-logs"
                        onClick={this.deselectAllLogs}
                      >
                        {_('Deselect All')}
                      </Link>
                    </ColumnLayout.Column>
                  </ColumnLayout.Row>
                </ColumnLayout.Column>
              </ColumnLayout.Row>

              <ColumnLayout.Row>
                <ColumnLayout.Column span={4} style={colStyle}>
                  <div>{metricsSwitches}</div>
                  <Message
                    id="metrics-error-message"
                    fill
                    type="error"
                    style={{ visibility: this.state.showMetricsError ? 'visible' : 'hidden' }}
                  >
                    {_('You must select at least one metric.')}
                  </Message>
                  <p>{_('Metric Options')}</p>
                  <div id="metric-options">{metricOptionsSwitches}</div>
                </ColumnLayout.Column>

                <ColumnLayout.Column span={8} style={colStyle}>
                  <div>{logSourcesSwitches}</div>
                  <div>{this.displayCustomLogs()}</div>
                  <Link
                    style={this.styles.addNewSource}
                    onClick={this.addNewCustomLog}
                    id="add-custom-source"
                  >
                    {_('+ Add Custom Source')}
                  </Link>
                </ColumnLayout.Column>
              </ColumnLayout.Row>
            </ColumnLayout>
          </Modal.Body>
          <Modal.Footer>
            <Button onClick={this.cancelAndCloseModal} label="Cancel" />
            <Button appearance="primary" onClick={this.submit} label="Save" disabled={!this.state.saveBtnEnabled} />
          </Modal.Footer>
        </Modal>
      </div>
    );
  }
}

export default CustomizeInputsModal;



