import * as actions from '../actionTypes';

const defaultHECSourcetype = 'em_metrics';
const defaultHECIndex = 'em_metrics';
const defaultHECName = 'http://splunk_app_infrastructure';

/**
 * Action to indicate initiation of enable HEC requests
 * @export
 * @returns - an action object
 */
export function enableHEC() {
  return {
    type: actions.ENABLE_HEC_INITIATE,
  };
}

/**
 * Action to indicate the state of the enable HEC action
 * @export
 * @param {string} status - the status of the call
 * @param {any} settings - the current HEC settings
 * @returns - an action object
 */
export function enableHecStatus(status, settings) {
  return {
    type: actions.ENABLE_HEC,
    status,
    settings,
  };
}

/**
 * Action to indicate initiation of HEC token fetch
 * @export
 * @param {any} [tokenName=defaultHECName] - the HEC token name
 * @param {any} [sourcetype=defaultHECSourcetype] - the sourcetype for the endpoint
 * @param {any} [index=defaultHECIndex] - the index used by the endpoint
 * @returns {Object} - an action object
 */
export function fetchHECToken(
  tokenName = defaultHECName,
  sourcetype = defaultHECSourcetype,
  index = defaultHECIndex,
) {
  return {
    type: actions.FETCH_HEC_TOKEN_INITIATE,
    tokenName,
    sourcetype,
    index,
  };
}


/**
 * Action to indicate the state of the HEC token fetch
 *
 * @export
 * @param {any} status - the fetch status
 * @param {any} response - the response object
 * @returns - an action object
 */
export function fetchHECTokenStatus(status, response) {
  return {
    type: actions.FETCH_HEC_TOKEN,
    status,
    response,
  };
}

/**
 * Action to indicate initiation of agent URL fetch
 * @export
 * @param  {any} [collector=collectd]  - collector name
 * @returns - an action object
 */
export function fetchAgentUrl(collector = 'collectd') {
  return {
    type: actions.FETCH_AGENT_URL_INITIATE,
    collector,
  };
}

/**
 * Action to indicate the fetching of the collector Agent URL
 * @export
 * @param {any} agentUrl The agent URL
 * @returns - an action object
 */
export function fetchAgentUrlStatus(agentUrl) {
  return {
    type: actions.FETCH_AGENT_URL,
    agent_url: agentUrl,
  };
}

/**
 * Action to deal with host name modifications
 * @export
 * @param {any} hostName - the hostname string
 * @returns {Object} - an action object
 */
export function changeSplunkHostName(hostName) {
  const values = hostName.split(':');
  const host = values[0];
  const port = values[1];
  return {
    type: actions.CHANGE_HOSTNAME,
    displayString: hostName,
    customHostName: host,
    customHecPort: port,
  };
}

/**
 * Handle Configuration Status Entities Count
 * @param  {int} count  - Entities Count from Configuration Status view
 * @return {Object} - an action object with the results of the request
 */
export function handleCount(count) {
  return {
    type: actions.UPDATE_COUNT,
    count,
  };
}

/**
 * Action fired upon saving Metrics and Logs
 * @param
 * @return
 */
export function updateCustomMetricsAndLogs({
  selectedMetrics, selectedMetricOptions, selectedLogs, customLogs }) {
  return {
    type: actions.UPDATE_CUSTOM_METRICS_AND_LOGS,
    selectedMetrics,
    selectedMetricOptions,
    selectedLogs,
    customLogs,
  };
}



