import { takeLatest, put, call, all } from 'redux-saga/effects';
import _ from 'lodash';
import * as awsAPI from '../services/aws';
import {
  addNewCloudWatchInput,
  cloudWatchRegionsRetrieveTracker,
} from '../actions/configure/awsConfiguration/CloudWatchInputCreation';
import {
  updateCloudWatchInputTracker,
} from '../actions/configure/awsConfiguration/CloudWatchInputUpdate';
import {
  cloudWatchInputsRetrieveTracker,
} from '../actions/configure/awsConfiguration/CloudWatchInputRetrieve';
import {
  cloudWatchLogsInputBulkEditTracker,
} from '../actions/configure/awsConfiguration/CloudWatchLogsInputBulkEdit';
import {
  cloudWatchLogsInputsRetrieveTracker,
} from '../actions/configure/awsConfiguration/CloudWatchLogsInputRetrieve';
import { awsCreateAccountTracker } from '../actions/configure/awsConfiguration/AwsAccountCreation';
import { awsUpdateAccountTracker } from '../actions/configure/awsConfiguration/AwsAccountUpdate';
import { awsDeleteAccountTracker } from '../actions/configure/awsConfiguration/AwsAccountDeletion';
import { awsAccountInfoRetrieveTracker } from '../actions/configure/awsConfiguration/AwsAccountRetrieve';
import * as actionTypes from '../actions/actionTypes';

/**
* Helper function to parse the response from the cloudwatch input endpoint
*/
function parseCloudWatchInputResponse(response) {
  const { entry } = response;
  const parsedResponse = entry.map((e) => {
    const name = e.name;
    const awsAccount = e.content.aws_account;
    const awsRegion = e.content.aws_region;
    const metricNamespace = e.content.metric_namespace;

    return {
      inputName: name,
      awsAccount,
      awsRegion: awsRegion.split(','),
      metricNamespace: _.uniq(JSON.parse(metricNamespace))
        .map(n => n.replace('AWS/', '')),
    };
  });
  return parsedResponse;
}

/**
* create cloudwatch input saga, this generator takes care of
* creating cloudwatch input
* dispatches status action START/SUCCESS/ERROR
*
* @memberOf awsSaga
* @param action
*/
export function* createCloudWatchInput(action) {
  const data = action;
  yield put(addNewCloudWatchInput.start());
  try {
    const { response } = yield call(awsAPI.addCloudWatchInput, data);
    const parsedResponse = parseCloudWatchInputResponse(response);
    yield put(addNewCloudWatchInput.success(parsedResponse));
  } catch (error) {
    yield put(addNewCloudWatchInput.failure(error));
  }
}

/**
* udpate cloudwatch input saga, this generator takes care of
* updating cloudwatch input
* dispatches status action START/SUCCESS/ERROR
*
* @memberOf awsSaga
* @param action
*/
export function* updateCloudWatchInput(action) {
  const data = action;
  yield put(updateCloudWatchInputTracker.start());
  try {
    const { response } = yield call(awsAPI.updateCloudWatchInput, data);
    const parsedResponse = parseCloudWatchInputResponse(response);
    yield put(updateCloudWatchInputTracker.success(parsedResponse));
    return true;
  } catch (error) {
    yield put(updateCloudWatchInputTracker.failure(error));
    return false;
  }
}

/**
* Load cloudwatch inputs saga, this generator takes care of
* fetching both a single cloudwatch input's info
* and all cloudwatch inputs' info
* dispatches status action START/SUCCESS/ERROR
*
* @memberOf awsSaga
* @param action with or without attribute inputName
*/
export function* loadCloudWatchInputs(action) {
  const { inputName } = action;
  yield put(cloudWatchInputsRetrieveTracker.start());
  try {
    let apiResponse;
    if (inputName) {
      apiResponse = yield call(awsAPI.loadCloudWatchInput, inputName);
    } else {
      apiResponse = yield call(awsAPI.loadAllCloudWatchInputs);
    }
    const { response } = apiResponse;
    const parsedResponse = parseCloudWatchInputResponse(response);
    yield put(cloudWatchInputsRetrieveTracker.success(parsedResponse));
  } catch (error) {
    yield put(cloudWatchInputsRetrieveTracker.failure(error));
  }
}

/**
* Helper function to parse the response from the aws regions endpoint
*/
function parseRegionsResponse(response) {
  const { entry } = response;
  return entry.map(e => ({ name: e.name, label: e.content.label }));
}

/**
* Helper function to parse the response from the aws create account endpoint
*/
function parseAwsAccountInfoResponse(response) {
  const { entry } = response;
  if (entry && entry[0] && entry[0].content) {
    return {
      name: entry[0].content.name,
      keyId: entry[0].content.key_id,
      category: entry[0].content.category,
    };
  }
  return {};
}

/**
* Load aws regions saga, this generator takes care of
* fetching aws regions info
* dispatches status action START/SUCCESS/ERROR
*
* @memberOf awsSaga
* @param action with attribute awsAccount
*/
export function* loadCloudWatchRegions(action) {
  const { awsAccount } = action;
  yield put(cloudWatchRegionsRetrieveTracker.start());
  try {
    const { response } = yield call(awsAPI.loadCloudWatchRegions, awsAccount);
    const parsedResponse = parseRegionsResponse(response);
    yield put(cloudWatchRegionsRetrieveTracker.success(parsedResponse));
  } catch (error) {
    yield put(cloudWatchRegionsRetrieveTracker.failure(error));
  }
}

/**
 * Returns a dispatch function that posts to ta_aws account endpoint
 * to create aws account
 * @returns {Function} - a dispatch function
 */
export function* createAwsAccount(action) {
  yield put(awsCreateAccountTracker.start());
  try {
    const { response } = yield call(awsAPI.createAwsAccount, action);
    const accountInfo = parseAwsAccountInfoResponse(response);
    yield put(awsCreateAccountTracker.success(accountInfo));
  } catch (error) {
    yield put(awsCreateAccountTracker.failure(error));
  }
}

/**
 * Returns a dispatch function that posts to ta_aws account endpoint
 * for account updating.
 * @returns {Function} - a dispatch function
 */
export function* updateAwsAccount(action) {
  yield put(awsUpdateAccountTracker.start());
  try {
    const { response } = yield call(awsAPI.updateAwsAccount, action);
    const accountInfo = parseAwsAccountInfoResponse(response);
    yield put(awsUpdateAccountTracker.success(accountInfo));
  } catch (error) {
    yield put(awsUpdateAccountTracker.failure(error));
  }
}

/**
 * Returns a dispatch functions that deletes the aws account.
 * @returns {Function} - a dispatch function
 */
export function* deleteAwsAccount(action) {
  try {
    const { response } = yield call(awsAPI.deleteAwsAccount, action);
    yield put(awsDeleteAccountTracker.success(response));
    return true;
  } catch (error) {
    yield put(awsDeleteAccountTracker.failure(error));
    return false;
  }
}

/**
 * Returns a dispatch function that GETs from ta_aws account endpoint
 * @returns {Function} - a dispatch function
 */
export function* loadAwsAccountInfo() {
  yield put(awsAccountInfoRetrieveTracker.start());
  try {
    const { response } = yield call(awsAPI.retrieveAwsAccountInfo);
    const accountInfo = parseAwsAccountInfoResponse(response);
    yield put(awsAccountInfoRetrieveTracker.success(accountInfo));
  } catch (error) {
    yield put(awsAccountInfoRetrieveTracker.failure(error));
  }
}

/**
* Helper function to parse the response from the cloudwatch logs input endpoint
*/
function parseCloudWatchLogsInputResponse(response) {
  const { entry } = response;
  const parsedResponse = entry.map((e) => {
    const name = e.name;
    const { account, region, groups } = e.content;
    return {
      name,
      account,
      region,
      group: groups,
    };
  });
  return parsedResponse;
}

/**
 * Load cloudwatch logs inputs saga, this generator takes care of
 * fetching both a single cloudwatch logs input's info
 * and all cloudwatch logs inputs' info
 * dispatches status action START/SUCCESS/ERROR
 */
export function* loadCloudWatchLogsInput({ inputName }) {
  yield put(cloudWatchLogsInputsRetrieveTracker.start());
  try {
    let apiResponse;
    if (inputName) {
      apiResponse = yield call(awsAPI.loadCloudWatchLogsInput, inputName);
    } else {
      apiResponse = yield call(awsAPI.loadAllCloudWatchLogsInput);
    }
    const { response } = apiResponse;
    const parsedResponse = parseCloudWatchLogsInputResponse(response);
    yield put(cloudWatchLogsInputsRetrieveTracker.success(parsedResponse));
  } catch (error) {
    yield put(cloudWatchLogsInputsRetrieveTracker.failure(error));
  }
}

/**
 * handles create/update/delete cloudwatch logs inputs
 *
 * @param string -- one of CREATE, UPDATE, DELETE
 * @param data -- input data
 */
export function* modifyCloudWatchLogsInput(actionType, data) {
  let actionAPI;
  switch (actionType) {
    case 'CREATE':
      actionAPI = awsAPI.addCloudWatchLogsInput;
      break;
    case 'UPDATE':
      actionAPI = awsAPI.updateCloudWatchLogsInput;
      break;
    case 'DELETE':
      actionAPI = awsAPI.deleteCloudWatchLogsInput;
      break;
    default:
      break;
  }
  try {
    const { response } = yield call(actionAPI, data);
    const parsedResponse = parseCloudWatchLogsInputResponse(response);
    const responseData = ['CREATE', 'UPDATE'].includes(actionType) ? parsedResponse[0] : data;
    return { status: `${actionType}_SUCCESS`, ...responseData };
  } catch (error) {
    return { status: `${actionType}_FAILURE`, error, ...data };
  }
}

/**
 * handles bulk create/update/delete cloudwatch logs inputs
 *
 * @param string -- one of CREATE, UPDATE, DELETE
 * @param account -- aws account name
 * @param inputs -- inputs to perform action on
 */
export function* bulkModifyCloudWatchLogsInput(actionType, account, inputs) {
  const actionStatus = yield all(
    inputs.map(input => call(
      modifyCloudWatchLogsInput,
      actionType,
      { account, ...input },
    )));
  return actionStatus;
}

/**
 * helper function that determines action on each cloudwatch logs input
 * based on if user chooses to enable cloudwatch logs, existing inputs and desired inputs
 *
 * @memberOf awsSaga
 * @param boolean -- if cloudwatch logs option is enabled
 * @param existing cloduwatch logs inputs before user modification
 * @param cloudwatch logs after user modification
 */
export const getCloudWatchLogsInputBulkActionItems = (useCloudWatchLogs, prevInputs, newInputs) => {
  const prevInputNames = prevInputs.map(input => input.name);
  const newInputNames = newInputs.map(input => input.name);
  const inputsToAdd = [];
  const inputsToDelete = [];
  const inputsToUpdate = [];
  if (!useCloudWatchLogs) {
    _.forEach(prevInputs, input => inputsToDelete.push(input));
  } else {
    const unionInputs = _.unionBy(prevInputs, newInputs, 'name');
    _.forEach(unionInputs, (input) => {
      if (prevInputNames.includes(input.name) && !newInputNames.includes(input.name)) {
        inputsToDelete.push(input);
      } else if (!prevInputNames.includes(input.name) &&
              newInputNames.includes(input.name)) {
        inputsToAdd.push(input);
      } else {
        // check if inputs needs to be updated
        const oldInput = _.find(prevInputs, { name: input.name });
        const modifiedInput = _.find(newInputs, { name: input.name });
        if (oldInput.group !== modifiedInput.group || oldInput.region !== modifiedInput.region) {
          inputsToUpdate.push(modifiedInput);
        }
      }
    });
  }
  return {
    inputsToAdd,
    inputsToDelete,
    inputsToUpdate,
  };
};

/**
 * bulk process cloudwatch logs input saga, this generator takes care of
 * creating/updating/deleting cloudwatch logs input
 * dispatches status action COMPLETE
 *
 * @memberOf awsSaga
 * @param account
 * @param old inputs
 * @param new inputs
 */
export function* bulkProcessCloudWatchLogsInput({
  account,
  useCloudWatchLogs,
  prevInputs,
  newInputs,
}) {
  yield put(cloudWatchLogsInputBulkEditTracker.start());
  // find inputs to delete/add/update
  const actionItems =
    getCloudWatchLogsInputBulkActionItems(useCloudWatchLogs, prevInputs, newInputs);
  const [createStatus, updateStatus, deleteStatus] = yield all([
    call(bulkModifyCloudWatchLogsInput, 'CREATE', account, actionItems.inputsToAdd),
    call(bulkModifyCloudWatchLogsInput, 'UPDATE', account, actionItems.inputsToUpdate),
    call(bulkModifyCloudWatchLogsInput, 'DELETE', account, actionItems.inputsToDelete),
  ]);
  const actionStatus = {};
  let successFlag = true;
  _.forEach([...createStatus, ...updateStatus, ...deleteStatus], (inputStatus) => {
    actionStatus[inputStatus.name] = inputStatus;
    successFlag = successFlag && _.endsWith(inputStatus.status, 'SUCCESS');
  });
  yield put(cloudWatchLogsInputBulkEditTracker.complete(actionStatus));
  return successFlag;
}

export function* deleteInputsLogInputsAccount(action) {
  const { inputArgs, logInputArgs, accountArgs } = action;
  // set accountDeletionState to IN_PROGRESS
  yield put(awsDeleteAccountTracker.start());
  // yield* guarantees sequential execution of sagas.
  const [deleteInputStatus, deleteInputLogsStatus] = yield all([
    call(updateCloudWatchInput, inputArgs),
    call(bulkProcessCloudWatchLogsInput, logInputArgs),
  ]);
  if (deleteInputStatus && deleteInputLogsStatus) {
    yield* deleteAwsAccount(accountArgs);
  } else {
    yield put(awsDeleteAccountTracker.failure(
      'Deletion of cloudwatch inputs / log inputs failed. Hence, the account was not deleted.'));
  }
}

export default function* awsSaga() {
  yield takeLatest(actionTypes.CLOUDWATCH_INPUT_RETRIEVE, loadCloudWatchInputs);
  yield takeLatest(actionTypes.CLOUDWATCH_INPUT_CREATE, createCloudWatchInput);
  yield takeLatest(actionTypes.CLOUDWATCH_REGIONS_RETRIEVE, loadCloudWatchRegions);
  yield takeLatest(actionTypes.CLOUDWATCH_INPUT_UPDATE, updateCloudWatchInput);
  yield takeLatest(actionTypes.AWS_ACCOUNT_CREATE, createAwsAccount);
  yield takeLatest(actionTypes.AWS_ACCOUNT_RETRIEVE, loadAwsAccountInfo);
  yield takeLatest(actionTypes.AWS_ACCOUNT_UPDATE, updateAwsAccount);
  yield takeLatest(actionTypes.AWS_ACCOUNT_DELETE, deleteInputsLogInputsAccount);
  yield takeLatest(actionTypes.CLOUDWATCH_LOGS_INPUT_RETRIEVE, loadCloudWatchLogsInput);
  yield takeLatest(actionTypes.CLOUDWATCH_LOGS_INPUT_BULK_EDIT, bulkProcessCloudWatchLogsInput);
}



