import React, { Children } from 'react';
import PropTypes from 'prop-types';
import { extend } from 'lodash';
import CustomListItem from './CustomListItem';
import { createTestHook } from '../../../util/TestSupport';

export default class CustomList extends React.Component {
  static propTypes = {
    children: PropTypes.node,
    style: PropTypes.object,
  }

  static defaultProps = {
    type: 'disc',
    children: null,
    style: null,
  }

  static Item = CustomListItem

  css = {
    margin: '16px 0px 21px 26px',
    listStyleType: 'none',
  }

  render() {
    return (
      <ol style={extend(this.css, this.props.style)} {...createTestHook(__filename, name)}>
        {Children.map(this.props.children, (child, idx) =>
          React.cloneElement(child, { itemNumber: idx + 1 }))}
      </ol>
    );
  }
}



