import React from 'react';
import PropTypes from 'prop-types';
import P from '@splunk/react-ui/Paragraph';
import Button from '@splunk/react-ui/Button';
import Link from '@splunk/react-ui/Link';
import RotateCounter from '@splunk/react-icons/RotateCounter';
import { _ } from '@splunk/ui-utils/i18n';
import { createTestHook } from '../../../util/TestSupport';

import DataManagerModel from '../../model/DataManagerModel';
import SPLStore from '../../../stores/SPLStore';
import EMUtils from '../../../libs/EMUtils';

const ENTITIES_LIST_URL = EMUtils.getAppPageUrl('investigate');
const CONNECTION_STATUS_STYLE = { marginBottom: '0px' };
const REFRESH_BUTTON_STYLE = { marginLeft: '5px' };
const REFRESH_TEXT_STYLES = {
  fontSize: 10,
  fontWeight: 100,
  color: '#999999',
};

const REFRESH_TIME = 60;

export default class ConfigurationStatus extends React.Component {
  static propTypes = {
    newCount: PropTypes.number,
    handleCount: PropTypes.func,
  }

  static defaultProps = {
    newCount: 0,
    handleCount: null,
  }

  constructor(props) {
    super(props);

    this.state = { timer: REFRESH_TIME };

    this.handleCountFetched = this.handleCountFetched.bind(this);
    this.onRefreshClicked = this.onRefreshClicked.bind(this);
    this.getMessage = this.getMessage.bind(this);
    this.updateTimer = this.updateTimer.bind(this);
  }

  componentDidMount() {
    this.intervalId = setInterval(this.updateTimer, 1000);
  }

  componentWillUnmount() {
    clearInterval(this.intervalId);
  }

  onRefreshClicked() {
    this.entitiesCountModel.forceUpdate();
    this.setState({
      timer: REFRESH_TIME,
    });
  }

  getMessage() {
    if (this.props.newCount === 0) {
      return _('No new entities connected yet...');
    }
    return _(`Congratulations, you've got
      ${this.props.newCount} ${this.props.newCount === 1 ? 'entity' : 'entities'} coming in.`);
  }


  handleCountFetched(results) {
    if (!results.data || !results.data[0] || !results.data[0][0]) {
      return;
    }

    const value = parseInt(results.data[0][0], 10);
    if (isNaN(value)) {
      return;
    }

    this.props.handleCount(value);
  }

  updateTimer() {
    if (this.state.timer === 0) {
      // refresh
      this.entitiesCountModel.forceUpdate();
    }

    this.setState({
      timer: this.state.timer === 0 ? REFRESH_TIME : this.state.timer - 1,
    });
  }

  render() {
    return (
      <div {...createTestHook(__filename, name)}>
        <P style={CONNECTION_STATUS_STYLE}>{this.getMessage()}
          {this.props.newCount !== 0 &&
            <Link
              openInNewContext
              to={ENTITIES_LIST_URL}
            >Take a look now</Link>
          }
        </P>
        <P style={REFRESH_TEXT_STYLES}>Auto refresh in {this.state.timer}s
          <Button
            label={_('Refresh')}
            style={REFRESH_BUTTON_STYLE}
            icon={<RotateCounter screenReaderText={null} />}
            appearance="pill"
            onClick={this.onRefreshClicked}
          />
        </P>
        <DataManagerModel
          ref={(ref) => { this.entitiesCountModel = ref; }}
          earliest_time="-1h"
          latest_time="now"
          search={SPLStore.getNumOfEntities()}
          onStateChange={this.handleCountFetched}
        />
      </div>
    );
  }
}



