import {
  CLOUDWATCH_INPUT_UPDATE,
  START,
  SUCCESS,
  FAILURE,
} from '../../actionTypes';
import ActionUtils from '../../actionUtils';

// This part will be used for saga
export const CLOUDWATCH_INPUT_UPDATE_TRACKER =
  ActionUtils.createRequestTypes(CLOUDWATCH_INPUT_UPDATE);

export const updateCloudWatchInputTracker = {
  start: () => ActionUtils.createAction(CLOUDWATCH_INPUT_UPDATE_TRACKER[START], {}),
  success: response => ActionUtils.createAction(CLOUDWATCH_INPUT_UPDATE_TRACKER[SUCCESS],
    { response }),
  failure: error => ActionUtils.createAction(CLOUDWATCH_INPUT_UPDATE_TRACKER[FAILURE],
    { error }),
};

export const updateCloudWatchInput =
  (name, accountName, awsRegions, selectedNamespaces, customDimensions) => ({
    type: CLOUDWATCH_INPUT_UPDATE,
    name,
    accountName,
    awsRegions,
    selectedNamespaces,
    customDimensions,
  });



