import React from 'react';
import PropTypes from 'prop-types';
import { pick } from 'lodash';

import TabLayout from '@splunk/react-ui/TabLayout';
import Heading from '@splunk/react-ui/Heading';
import Modal from '@splunk/react-ui/Modal';
import { _ } from '@splunk/ui-utils/i18n';

import AwsConfigurationContainer from '../../containers/configure/types/AwsConfiguration';
import UnixConfiguration from './types/unixConfiguration';
import { createTestHook } from '../../util/TestSupport';


export default class ConfigurePageContents extends React.Component {
  static propTypes = {
    ...UnixConfiguration.propTypes,
    onCustomDimensionChange: PropTypes.func.isRequired,
  }

  static defaultProps = { ...UnixConfiguration.defaultProps }

  constructor(props, ...rest) {
    super(props, ...rest);

    this.state = {
      modalMessageState: false,
      modalMessage: '',
    };

    this.styles = {
      main: {
        margin: 'auto auto',
        paddingTop: '20px',
        paddingLeft: '20px',
      },
    };
  }

  displayMessage = (message) => {
    this.setState({
      modalMessageState: true,
      modalMessage: message,
    });
  }

  handleModalRequestClose = () => {
    this.setState({
      modalMessageState: false,
    });
  }

  handlePanelChange = () => {
    this.props.onCustomDimensionChange([]);
  }

  render() {
    const unixConfigurationProps = pick(this.props, Object.keys(UnixConfiguration.propTypes));
    return (
      <div {...createTestHook(__filename, name)}>
        <Modal onRequestClose={this.handleModalRequestClose} open={this.state.modalMessageState}>
          <Modal.Body>
            {this.state.modalMessage}
          </Modal.Body>
        </Modal>
        <Heading style={this.styles.main} level={1}>{_('Configure Integrations')}</Heading>
        <TabLayout
          defaultActivePanelId="linux"
          tabWidth={150}
          layout="vertical"
          style={{ backgroundColor: 'white' }}
          onChange={this.handlePanelChange}
        >
          <TabLayout.Panel label="Linux" panelId="linux">
            <UnixConfiguration {...unixConfigurationProps} />
          </TabLayout.Panel>
          <TabLayout.Panel label="AWS" panelId="aws">
            <AwsConfigurationContainer />
          </TabLayout.Panel>
        </TabLayout>
      </div>
    );
  }
}



