import {
  SOURCETYPES_RETRIEVE,
  START,
  SUCCESS,
  FAILURE,
} from '../../actionTypes';
import ActionUtils from '../../actionUtils';

// This part will be used for saga
export const SOURCETYPES_RETRIEVE_TRACKER =
  ActionUtils.createRequestTypes(SOURCETYPES_RETRIEVE);

export const sourcetypesRetrieveTracker = {
  start: () => ActionUtils.createAction(SOURCETYPES_RETRIEVE_TRACKER[START], {}),
  success: response => ActionUtils.createAction(SOURCETYPES_RETRIEVE_TRACKER[SUCCESS],
    { response }),
  failure: error => ActionUtils.createAction(SOURCETYPES_RETRIEVE_TRACKER[FAILURE],
    { error }),
};

export const onLoadAllSourceTypes = () => ({
  type: SOURCETYPES_RETRIEVE,
});



