import {
  AWS_ACCOUNT_DELETE,
  AWS_ACCOUNT_DELETION_CONFIRM,
  AWS_ACCOUNT_DELETION_READY,
  START,
  SUCCESS,
  FAILURE,
} from '../../actionTypes';
import ActionUtils from '../../actionUtils';

// This part will be used for saga
export const AWS_ACCOUNT_DELETE_TRACKER =
  ActionUtils.createRequestTypes(AWS_ACCOUNT_DELETE);

export const awsDeleteAccountTracker = {
  start: () => ActionUtils.createAction(AWS_ACCOUNT_DELETE_TRACKER[START], {}),
  success: response => ActionUtils.createAction(AWS_ACCOUNT_DELETE_TRACKER[SUCCESS],
    { response }),
  failure: error => ActionUtils.createAction(AWS_ACCOUNT_DELETE_TRACKER[FAILURE],
    { error }),
};

/**
 * Action used to describe that aws account deletion state
 * is in "confirm", that is, the account to be deleted
 * is awaiting user confirmation
 */
export const accountDeletionConfirm = () => ({
  type: AWS_ACCOUNT_DELETION_CONFIRM,
});

/**
 * Action used to describe that account deletion state
 * is now "ready", that is either account has been deleted
 * or confirmation was cancelled
 */
export const accountDeletionReady = () => ({
  type: AWS_ACCOUNT_DELETION_READY,
});

/**
 * @param  {Object} inputArgs  - The arguments required to delete the cloudwatch input
 * @param  {Object} logInputArgs  - The arguments required to delete the cloudwatch input logs
 * @param  {Object} accountArgs  - The arguments required to delete the aws account
 * @return {Object} - an action object with the results of the request
 */
export const onDeleteAwsAccount = (inputArgs, logInputArgs, accountArgs) => ({
  type: AWS_ACCOUNT_DELETE,
  inputArgs,
  logInputArgs,
  accountArgs,
});



