import React from 'react';
import PropTypes from 'prop-types';
import lodash from 'lodash';
import Text from '@splunk/react-ui/Text';
import Link from '@splunk/react-ui/Link';
import P from '@splunk/react-ui/Paragraph';
import List from '@splunk/react-ui/List';
import ControlGroup from '@splunk/react-ui/ControlGroup';
import { _ } from '@splunk/ui-utils/i18n';
import WaitSpinner from '@splunk/react-ui/WaitSpinner';
import InfoCircle from '@splunk/react-icons/InfoCircle';
import Metrics from '@splunk/react-icons/Metrics';
import Events from '@splunk/react-icons/Events';
import { createTestHook } from '../../../util/TestSupport';
import { INFO_ICON } from '../../common/Colors';
import CustomList from '../../common/CustomList/CustomList';
import CopyableConfigText from '../../common/CopyableConfigText';
import DimensionMultiSelectContainer from '../../../containers/common/DimensionMultiSelect';
import ConfigurationStatus from '../../page_contents/configure_components/ConfigurationStatus';
import CustomizeInputsModal from '../CustomizeInputsModal';
import { SUPPORTED_NIX_OS, COLLECTOR_DEPENDENCIES } from '../../../util/StateConstants';
import { pluralizeNoun } from '../../../libs/EMUtils';


export default class UnixConfiguration extends React.Component {
  static propTypes = {
    /** boolean value indicate if HEC is enabled */
    isHttpInputEnabled: PropTypes.bool,
    /** boolean value indicate if is fetching HEC token */
    isFetchingToken: PropTypes.bool,
    /** hostname in textfield */
    displayHostName: PropTypes.string,
    /** hostname for HEC */
    hostName: PropTypes.string,
    /** callback to update hostname and hec port */
    updateSplunkHostName: PropTypes.func,
    /** HEC port to use */
    hecPort: PropTypes.string,
    /** HEC token to use */
    hecToken: PropTypes.string,
    /** link to collector package */
    packageLink: PropTypes.string,
    // Dimension MultiSelect
    selectedDimensions: PropTypes.arrayOf(PropTypes.arrayOf(PropTypes.string)),
    // Configuration Status
    connectedCount: PropTypes.number,
    newCount: PropTypes.number,
    initialFetchDone: PropTypes.bool,
    handleCount: PropTypes.func,
    sourceTypes: PropTypes.arrayOf(PropTypes.string),
    loadAllSourceTypes: PropTypes.func,
    /** TCP port to receive data sent from forwarder */
    receiverPort: PropTypes.string,
    /** Callback to update custom metrics and logs */
    updateCustomMetricsAndLogs: PropTypes.func.isRequired,
    selectedMetrics: PropTypes.array,
    selectedMetricOptions: PropTypes.array,
    metricOptionLabels: PropTypes.object,
    /** Log files to be monitored */
    selectedLogs: PropTypes.object,
  };

  static defaultProps = {
    isHttpInputEnabled: false,
    initialFetchDone: false,
    displayHostName: '',
    hostName: '',
    updateSplunkHostName: () => {},
    hecPort: '',
    hecToken: '',
    packageLink: '',
    selectedDimensions: [],
    // Configuration Status
    connectedCount: 0,
    newCount: 0,
    handleCount: () => {},
    isFetchingToken: true,
    receiverPort: '9997',
    sourceTypes: [],
    loadAllSourceTypes: null,
    selectedMetrics: ['cpu', 'df', 'disk', 'interface', 'load', 'memory'],
    selectedMetricOptions: ['cpu.by_cpu'],
    metricOptionLabels: { 'cpu.by_cpu': `[cpu] ${_('Collect data for each cpu')}` },
    selectedLogs: {
      '/var/log/syslog': 'syslog',
      '/var/log/daemon.log': 'syslog',
      '/var/log/auth.log': 'syslog',
    },
  };

  /**
   * Examines the URL to determine if this is a cloud environment
   *
   * @returns {Boolean} - true, if this is a cloud instance
   *
   * @memberOf UnixConfiguration
   */
  static isSplunkCloud() {
    return window.location.hostname.endsWith('cloud.splunk.com') || window.location.hostname.endsWith('splunkcloud.com');
  }

  constructor(props) {
    super(props);

    this.styles = {
      title: {
        fontSize: 14,
        fontWeight: 600,
      },
      InfoCircleStyle: {
        margin: '0px 15px 0px 30px',
      },
    };

    // Keeping this state as internal since this exposes the mechanism
    // of modifying the group details
    this.state = {
      customizeInputsModalOpen: false,
    };
  }

  handleSplunkHostnameChange = (e, { value }) => {
    this.props.updateSplunkHostName(value);
  }

  openCustomizeInputsModal = () => {
    this.setState({
      customizeInputsModalOpen: true,
    });
    this.props.loadAllSourceTypes();
  }

  closeCustomizeInputsModal = () => {
    this.setState({
      customizeInputsModalOpen: false,
    });
  }

  stringifyLogs = logObject => lodash.map(logObject, (value, key) => `${key}%${value}`);

  pluralize = pluralizeNoun;

  render() {
    const flattenedDimensions = this.props.selectedDimensions.map(([category, value]) => `${category}:${value}`).join(',');
    const shouldRenderInstallCommand = this.props.isHttpInputEnabled && !this.props.isFetchingToken;
    const agentFileName = lodash.last(this.props.packageLink ? this.props.packageLink.split('/') : []);
    const selectedMetricsLength = this.props.selectedMetrics.length;
    const selectedLogsLength = lodash.keys(this.props.selectedLogs).length;
    const supportedNIXOSText = SUPPORTED_NIX_OS.join(', ');
    const collectorDependencies = COLLECTOR_DEPENDENCIES;

    /* eslint-disable no-nested-ternary */
    return (
      <div style={{ paddingTop: '15px' }} {...createTestHook(__filename, name)}>
        <CustomizeInputsModal
          handleRequestClose={this.closeCustomizeInputsModal}
          open={this.state.customizeInputsModalOpen}
          updateCustomMetricsAndLogs={this.props.updateCustomMetricsAndLogs}
          sourceTypes={this.props.sourceTypes}
          selectedMetrics={this.props.selectedMetrics}
          selectedMetricOptions={this.props.selectedMetricOptions}
          metricOptionLabels={this.props.metricOptionLabels}
          selectedLogs={this.props.selectedLogs}
        />
        <P style={{ marginLeft: '25px', marginBottom: '0px' }}>{_('Follow the instructions to install an agent on your Linux machine.')}</P>
        <div style={{ marginTop: '20px ' }}>
          <InfoCircle style={this.styles.InfoCircleStyle} size={1.25} color={INFO_ICON} />
          {_('Supported OS types')}: {supportedNIXOSText}.
        </div>
        <CustomList>
          <CustomList.Item>
            <div>
              <P style={this.styles.title}>{_('Specify configuration options')}</P>
              <ControlGroup label={_('Data to be collected')}>
                <div id="metrics-logs-summary" style={{ paddingTop: '5px' }} >
                  <Metrics style={{ fontSize: '20px', marginRight: '5px' }} />
                  {selectedMetricsLength} {this.pluralize(selectedMetricsLength, 'Metric')}
                  {' + '} <Events style={{ fontSize: '20px', margin: '0 5px' }} />
                  {selectedLogsLength} {this.pluralize(selectedLogsLength, 'Log source')}
                  <Link
                    id="open-custom-data-modal"
                    onClick={this.openCustomizeInputsModal}
                    style={{ marginLeft: '15px' }}
                  >
                    {_('Customize')}
                  </Link>
                </div>
              </ControlGroup>
              <ControlGroup
                label={_('Dimensions')}
                help={
                  <span>
                    {_('Dimensions are key/value pairs used for troubleshooting, analysis, and filtering hosts, for example ')}
                    <i>{_('location:seattle')}</i> {_('or')} <i>{_('role:webserver.')}</i>
                  </span>
                }
              >
                <DimensionMultiSelectContainer
                  multiselectStyle={{ width: '100%' }}
                  allowNewDimensions
                />
              </ControlGroup>
              {!UnixConfiguration.isSplunkCloud() &&
              <ControlGroup
                label={_('Monitoring machine')}
                help={
                  <span>
                    {_('Specify the hostname or IP address and port of '
                      + 'the machine you want to send data to, '
                      + 'e.g. my.instance.domain.name:8088')}
                  </span>
                }
              >
                <Text
                  inputId="monitoring-machine-input"
                  canClear
                  value={this.props.displayHostName}
                  placeholder={_('Hostname or IP address and optional port')}
                  onChange={this.handleSplunkHostnameChange}
                />
              </ControlGroup>
              }
            </div>
          </CustomList.Item>
          <CustomList.Item>
            <P style={this.styles.title}>{_('Copy and paste the following into the command line of your entity')}</P>
            <P>{_('This script must be run by user with root privileges, and it should also have the following dependencies available on the entity:')}</P>
            <List>
              {collectorDependencies.map(dependency => (
                <List.Item>{_(dependency)}</List.Item>
              ))}
            </List>

            {shouldRenderInstallCommand && this.props.packageLink ? (
              <CopyableConfigText>
                {
                  `export SPLUNK_URL=${this.props.hostName}`
                  + ` && export HEC_PORT=${this.props.hecPort}`
                  + ` && export RECEIVER_PORT=${this.props.receiverPort}`
                  + ` && export HEC_TOKEN=${this.props.hecToken}`
                  + ` && export DIMENSIONS=${flattenedDimensions}`
                }
                {` METRIC_TYPES=${this.props.selectedMetrics}`}
                {` METRIC_OPTS=${this.props.selectedMetricOptions}`}
                {` LOG_SOURCES=${this.stringifyLogs(this.props.selectedLogs)}`}
                {
                  ` && curl -ksL -o ${agentFileName}`
                  + ` ${window.location.protocol}//${this.props.hostName}:${window.location.port}`
                  + `${this.props.packageLink}`
                  + ` && tar -xzf ${agentFileName} && cd unix-agent `
                  + `${lodash.size(this.props.selectedLogs) !== 0 ? '&& bash install_uf.sh ' : ''}`
                  + '&& bash install_agent.sh && cd .. && rm -rf unix-agent'
                }
              </CopyableConfigText>
            ) : this.props.packageLink ? (
              <div>
                <WaitSpinner size="medium" />
              </div>
            ) : ''
            }
          </CustomList.Item>
          <CustomList.Item>
            <P style={this.styles.title}>{_('Once the script finishes running, verify your data connection.')}</P>
            <P>{_('It may take up to 5 minutes to show entities in the UI.')}</P>
            <ConfigurationStatus
              connectedCount={this.props.connectedCount}
              newCount={this.props.newCount}
              initialFetchDone={this.props.initialFetchDone}
              handleCount={this.props.handleCount}
            />
          </CustomList.Item>
        </CustomList>
      </div>
    );
  }
}



