import { takeLatest, put, call } from 'redux-saga/effects';
import { normalizeBoolean } from '@splunk/ui-utils/boolean';
import { _ } from '@splunk/ui-utils/i18n';
import { filter, map } from 'lodash';
import * as hec from '../services/hec';
import * as configureActions from '../actions/configure/ConfigurePageContents';
import * as actionTypes from '../actions/actionTypes';
import loadAllSourceTypesRest from '../services/splunk/sourcetypes';
import {
  sourcetypesRetrieveTracker,
} from '../actions/configure/unixConfiguration/SourceTypesRetrieve';

/**
 * These sagas are all chained together to avoid race conditions.
 * enableHttpInput => fetchHECToken => fetchCollectorInstallLink
 * TODO: there's probably a better way to do this but it can wait.
 */

/**
 * Returns a dispatch function that fetches or creates the HEC token for data collectors
 *
 * @export
 * @returns {Function} - a dispatch function
 */
export function* enableHttpInput() {
  const hecSettings = {
    splunkHttpInputEnabled: false,
    hecPort: null,
    statusText: null,
  };

  yield put(configureActions.enableHecStatus('start', {}));

  const { response } = yield call(hec.getHttpInputXhr);
  const isDisabled = response.entry[0].content.disabled;
  const globalSetting = response.entry[0].content;
  hecSettings.hecPort = globalSetting.port;

  // Enable HEC if disabled
  if (normalizeBoolean(isDisabled)) {
    const enableResponse = yield call(hec.enableHec);
    if (enableResponse.response) {
      hecSettings.splunkHttpInputEnabled = true;
      hecSettings.statusText = _('Successfully enabled Http Event Collector.');
    } else {
      hecSettings.statusText = _('Unable to enable Http Event Collector: ') + enableResponse.error;
      yield put(configureActions.enableHecStatus('error', hecSettings));
      return;
    }
  } else {
    hecSettings.splunkHttpInputEnabled = true;
    hecSettings.statusText = _('Http Event Collector already enabled.');
  }

  yield put(configureActions.enableHecStatus('success', hecSettings));
  yield put(configureActions.fetchHECToken());
}

/**
 * Returns a dispatch function that fetches or creates the HECx token for data collectors
 *
 * @export
 * @returns {Function} - a dispatch function
 */
export function* fetchHECToken(action) {
  yield put(configureActions.fetchHECTokenStatus('start', {}));

  const status = {
    hecToken: null,
    sourcetype: action.sourcetype,
    index: action.index,
    statusText: null,
  };
  try {
    const { response } = yield call(hec.getEntityMonitoringHecTokensXhr);
    const token = response.entry[0].content;
    // update the token if the token definition does not match
    if (
      ('index' in token && 'sourcetype' in token) &&
      (token.index !== action.index ||
      token.sourcetype !== action.sourcetype ||
      normalizeBoolean(token.disabled))
    ) {
      try {
        const updateTokenResults = yield call(
          hec.updateExistingTokenXhr,
          action.tokenName,
          action.sourcetype,
          action.index,
        );
        status.hecToken = updateTokenResults.response.entry[0].content.token;
        status.statusText = _('Successfully fetched HEC token.');
      } catch (updateError) {
        status.statusText = _('Failed to update HEC token: ') + updateError.error;
        yield put(configureActions.fetchHECTokenStatus('error', status));
        return;
      }
    } else {
      status.hecToken = token.token;
      status.statusText = _('Successfully fetched HEC token.');
    }
  } catch (error) {
    if (error.status === 404) {
      // Create token if it doesn't exist
      try {
        const newTokenResults = yield call(
          hec.createNewTokenXhr,
          action.tokenName,
          action.sourcetype,
          action.index);

        status.hecToken = newTokenResults.response.entry[0].content.token;
        status.statusText = _('Successfully created HEC token.');
      } catch (createError) {
        status.statusText = _('Failed to create HEC token: ') + createError.error;
        yield put(configureActions.fetchHECTokenStatus('error', status));
        return;
      }
    } else {
      status.statusText = _('Failed to fetch HEC token: ') + error.message;
      yield put(configureActions.fetchHECTokenStatus('error', status));
      return;
    }
  }

  yield put(configureActions.fetchHECTokenStatus('success', status));
  yield put(configureActions.fetchAgentUrl());
}

/**
 * Get collector install path from conf file
 * @returns {Function} - a dispatch function
 */
export function* fetchCollectorInstallLink(action) {
  const { response } = yield call(hec.getCollectorUrl, action.collector);
  const agentUrl = response.entry[0].content.agent_url;
  yield put(configureActions.fetchAgentUrlStatus(agentUrl));
}

/**
* Retrieve sourcetypes saga, this generator takes care of
* fetching all the available sourcetypes in splunk_app_infrastructure
* dispatches status action START/SUCCESS/ERROR
*
* @memberOf unixSaga
*/
export function* loadAllSourceTypes() {
  yield put(sourcetypesRetrieveTracker.start());
  try {
    const { response } = yield call(loadAllSourceTypesRest);
    const parsedResponse = map(
      filter(response.entry, eachEntry => eachEntry.content.category),
      filteredEntry => filteredEntry.name);
    yield put(sourcetypesRetrieveTracker.success(parsedResponse));
  } catch (error) {
    yield put(sourcetypesRetrieveTracker.failure(error));
  }
}

/**
 * Main saga generator, it could handle groups of multiple sagas
 * @export
 */
export default function* configureSaga() {
  yield takeLatest(actionTypes.ENABLE_HEC_INITIATE, enableHttpInput);
  yield takeLatest(actionTypes.FETCH_HEC_TOKEN_INITIATE, fetchHECToken);
  yield takeLatest(actionTypes.FETCH_AGENT_URL_INITIATE, fetchCollectorInstallLink);
  yield takeLatest(actionTypes.SOURCETYPES_RETRIEVE, loadAllSourceTypes);
}



