import { createRESTURL } from '@splunk/splunk-utils/url';
import { requestAPI } from '../util';


const baseUrlConfig = {
  owner: 'nobody',
  app: 'splunk_app_infrastructure',
};

const SOURCETYPES_URL = createRESTURL('saved/sourcetypes', baseUrlConfig);

const loadAllSourceTypesRest = () => requestAPI(SOURCETYPES_URL, '', 'GET', { count: -1 });

export default loadAllSourceTypesRest;



