import React from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';

import {
  filterDimensions,
  updateSelectedDimensions,
} from '../../actions/common/DimensionMultiSelect';
import { loadDimensions } from '../../actions/entities/DimensionRetrieve';
import DimensionMultiSelect from '../../components/common/DimensionMultiSelect';
import { FETCH_OBJECT_STATE } from '../../util/StateConstants';

class DimensionMultiSelectContainer extends React.Component {
  static propTypes = { ...DimensionMultiSelect.propTypes };
  static defaultProps = { ...DimensionMultiSelect.defaultProps };

  componentDidMount() {
    if (this.props.filteredDimensions.length === 0
      && this.props.dimensionsFetchState !== FETCH_OBJECT_STATE.IN_PROGRESS) {
      this.props.loadDimensions();
    }
  }

  render() {
    return (
      <DimensionMultiSelect
        multiselectStyle={this.props.multiselectStyle}
        dimensionNames={this.props.filteredDimensionNames}
        dimensions={this.props.filteredDimensions}
        selectedDimensions={this.props.selectedDimensions}
        onFilterChange={this.props.onDimensionFilterChange}
        onValueChange={this.props.onCustomDimensionChange}
        placeholder={this.props.placeholder}
        allowNewDimensions={this.props.allowNewDimensions}
        dimensionsFetchState={this.props.dimensionsFetchState}
      />
    );
  }
}

const mapStateToProps = state => ({
  // Dimension Multiselect
  filteredDimensions: state.dimensionMultiselectReducer.filteredDimensions,
  filteredDimensionNames: state.dimensionMultiselectReducer.filteredDimensionNames,
  selectedDimensions: state.dimensionMultiselectReducer.selectedDimensions,
  dimensionsFetchState: state.dimensionMultiselectReducer.dimensionsFetchState,
});

const mapDispatchToProps = dispatch => bindActionCreators({
  onDimensionFilterChange: filterDimensions,
  onCustomDimensionChange: updateSelectedDimensions,
  loadDimensions,
}, dispatch);

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(DimensionMultiSelectContainer);



