import React from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { pick, isEqual, cloneDeep } from 'lodash';
import {
  updateSelectedDimensions,
} from '../../../actions/common/DimensionMultiSelect';
import {
  createCloudWatchInput,
  loadCloudWatchRegions,
} from '../../../actions/configure/awsConfiguration/CloudWatchInputCreation';
import { updateCloudWatchInput } from '../../../actions/configure/awsConfiguration/CloudWatchInputUpdate';
import { loadAllCloudWatchInputs } from '../../../actions/configure/awsConfiguration/CloudWatchInputRetrieve';
import {
  loadAllCloudWatchLogsInputs,
} from '../../../actions/configure/awsConfiguration/CloudWatchLogsInputRetrieve';
import { onCreateAwsAccount } from '../../../actions/configure/awsConfiguration/AwsAccountCreation';
import { onUpdateAwsAccount } from '../../../actions/configure/awsConfiguration/AwsAccountUpdate';
import { loadAwsAccountInfo } from '../../../actions/configure/awsConfiguration/AwsAccountRetrieve';
import {
  bulkEditCloudWatchLogsInput,
} from '../../../actions/configure/awsConfiguration/CloudWatchLogsInputBulkEdit';
import {
  handleCount,
} from '../../../actions/configure/ConfigurePageContents';
import AwsConfiguration from '../../../components/configure/types/AwsConfiguration';
import {
  AWS_ACCOUNT_STATUS,
  INPUT_MODIFY,
  FETCH_OBJECT_STATE,
  DELETE_OBJECT_STATE,
} from '../../../util/StateConstants';
import {
  accountDeletionConfirm,
  accountDeletionReady,
  onDeleteAwsAccount,
} from '../../../actions/configure/awsConfiguration/AwsAccountDeletion';

const CLOUDWATCH_INPUT_NAME = 'em_cloudwatch_input';

class AwsConfigurationContainer extends React.Component {
  static propTypes = { ...AwsConfiguration.propTypes }
  static defaultProps = { ...AwsConfiguration.defaultProps }

  constructor(props) {
    super(props);
    const isNewInput = this.props.initialInputName === '';
    const isNewAccount = this.props.initialName === '';
    const isNewLogInput = this.props.initialLogInputsInfo.length === 0;
    this.state = {
      name: this.props.initialName,
      keyId: this.props.initialKeyId,
      secretKey: this.props.initialSecretKey,
      selectedEntityTypes: this.props.initialSelectedEntityTypes,
      selectedRegions: this.props.initialSelectedRegions,
      accountRegionCategory: `${this.props.initialAccountRegionCategory}`,
      useCloudWatchLogs: !isNewLogInput,
      logInputsInfo: this.props.initialLogInputsInfo,
      isNewAccount,
      isNewInput,
      isNewLogInput,
    };
  }

  componentDidMount() {
    const isNew = this.state.isNewAccount || this.state.isNewInput || this.state.isNewLogInput;
    if (isNew &&
        this.props.isFetchingInput === FETCH_OBJECT_STATE.READY &&
        this.props.isFetchingAccountInfo === FETCH_OBJECT_STATE.READY &&
        this.props.isFetchingLogsInput === FETCH_OBJECT_STATE.READY) {
      this.props.onLoadCloudWatchRegions(this.state.name);
      this.props.onLoadAllCloudWatchInputs();
      this.props.onLoadFetchAccountInfo();
      this.props.onLoadAllCloudWatchLogsInputs();
    }

    const onTabSwitch = (
      this.props.initialSelectedDimensions.length > 0 &&
      this.props.selectedDimensions.length === 0
    );
    if (onTabSwitch) {
      this.props.onCustomDimensionChange(this.props.initialSelectedDimensions);
    }
  }

  componentWillReceiveProps(nextProps) {
    const updatingDimensions =
      !isEqual(nextProps.selectedDimensions, this.props.selectedDimensions);
    const updateAccount = nextProps.awsAccountStatus === AWS_ACCOUNT_STATUS.SUCCESS
      || nextProps.awsAccountStatus === AWS_ACCOUNT_STATUS.READY;
    const updateInput = nextProps.inputModifyState === INPUT_MODIFY.SUCCESS
      || nextProps.inputModifyState === INPUT_MODIFY.READY;
    const updatingLogInputsInfo =
      !isEqual(nextProps.initialLogInputsInfo, this.props.initialLogInputsInfo);
    if (updateAccount && !updatingDimensions) {
      this.setState({
        name: nextProps.initialName,
        keyId: nextProps.initialKeyId,
        accountRegionCategory: `${nextProps.initialAccountRegionCategory}`,
      });
    }
    if (updateInput && !updatingDimensions) {
      this.setState({
        selectedEntityTypes: nextProps.initialSelectedEntityTypes,
        selectedRegions: nextProps.initialSelectedRegions,
        isNewAccount: nextProps.initialName === '',
        isNewInput: nextProps.initialInputName === '',
      });
    }
    if (!isEqual(nextProps.initialSelectedDimensions, this.props.initialSelectedDimensions)) {
      this.props.onCustomDimensionChange(nextProps.initialSelectedDimensions);
    }
    if (updatingLogInputsInfo) {
      this.setState({
        logInputsInfo: nextProps.initialLogInputsInfo,
        useCloudWatchLogs: nextProps.initialLogInputsInfo.length > 0,
        isNewLogInput: nextProps.initialLogInputsInfo.length === 0,
      });
    }
    if (nextProps.accountDeletionState === DELETE_OBJECT_STATE.SUCCESS) {
      this.setState({
        useCloudWatchLogs: false,
        selectedEntityTypes: [],
      });
    }
  }

  onUpdateAccountForm = (name, value) => {
    this.setState({ [name]: value });
  }

  onSubmitAwsAccountChanges = () => {
    const createOrUpdate = this.state.isNewAccount ?
      this.props.onCreateAwsAccount :
      this.props.onUpdateAwsAccount;
    createOrUpdate(
      this.state.name,
      this.state.keyId,
      this.state.secretKey,
      this.state.accountRegionCategory,
    );
  }

  onUpdateSelectedRegions = (regions) => {
    this.setState({ selectedRegions: regions });
  }

  onUpdateSelectedEntityTypes = (entityType) => {
    const prevValues = this.state.selectedEntityTypes;
    if (prevValues.includes(entityType)) {
      this.setState({
        selectedEntityTypes: prevValues.filter(v => v !== entityType),
      });
    } else {
      this.setState({
        selectedEntityTypes: prevValues.concat(entityType),
      });
    }
  }

  onSubmitCloudWatchInputForm = () => {
    const createOrUpdate = this.state.isNewInput ?
      this.props.onCreateCloudWatchInput :
      this.props.onUpdateCloudWatchInput;
    createOrUpdate(
      CLOUDWATCH_INPUT_NAME,
      this.state.name,
      this.state.selectedRegions.join(','),
      this.state.selectedEntityTypes.map(type => `AWS/${type}`),
    );
  }

  onSubmitAccountDelete = () => {
    this.props.onDeleteAwsAccount({
      name: CLOUDWATCH_INPUT_NAME,
      accountName: this.state.name,
      awsRegions: '',
      selectedNamespaces: [],
      customDimensions: [],
    }, {
      account: this.props.initialName,
      useCloudWatchLogs: false,
      prevInputs: this.props.initialLogInputsInfo,
      newInputs: [],
    }, {
      name: this.state.name,
    });
  }

  onEnableCloudWatchLogs = () => {
    this.setState({
      useCloudWatchLogs: !this.state.useCloudWatchLogs,
    });
    this.onAddCloudWatchLogsInput();
  }

  onAddCloudWatchLogsInput = () => {
    const lastForm = this.state.logInputsInfo[this.state.logInputsInfo.length - 1];
    const lastFormFilled = (
      lastForm !== undefined &&
      lastForm.region !== undefined &&
      lastForm.group !== undefined);
    if (this.state.logInputsInfo.length === 0 || lastFormFilled) {
      this.setState({
        logInputsInfo: [
          ...this.state.logInputsInfo,
          { name: `cloudwatch-log-input-${Date.now()}` },
        ],
      });
    }
  }

  onRemoveCloudWatchLogsInput = (idx) => {
    const newLogInputsInfo = cloneDeep(this.state.logInputsInfo);
    newLogInputsInfo.splice(idx, 1);
    this.setState({
      logInputsInfo: newLogInputsInfo,
    });
  }

  onUpdateCloudWatchLogsInputGroupField = (idx, groupName) => {
    const newLogInputsInfo = cloneDeep(this.state.logInputsInfo);
    newLogInputsInfo[idx].group = groupName;
    this.setState({
      logInputsInfo: newLogInputsInfo,
    });
  }

  onUpdateCloudWatchLogsInputRegionSelection = (idx, region) => {
    const newLogInputsInfo = cloneDeep(this.state.logInputsInfo);
    newLogInputsInfo[idx].region = region;
    this.setState({
      logInputsInfo: newLogInputsInfo,
    });
  }

  onSubmitCloudWatchLogsInputForm = () => {
    this.props.onBulkEditCloudWatchLogsInput({
      account: this.props.initialName,
      useCloudWatchLogs: this.state.useCloudWatchLogs,
      prevInputs: this.props.initialLogInputsInfo,
      newInputs: this.state.logInputsInfo,
    });
  }

  render() {
    const props = {
      ...pick({ ...this.props, ...this.state }, Object.keys(AwsConfiguration.propTypes)),
      ...{
        onUpdateAccountForm: this.onUpdateAccountForm,
        onSubmitAwsAccountChanges: this.onSubmitAwsAccountChanges,
        onUpdateSelectedRegions: this.onUpdateSelectedRegions,
        onUpdateSelectedEntityTypes: this.onUpdateSelectedEntityTypes,
        onSubmitCloudWatchInputForm: this.onSubmitCloudWatchInputForm,
        onEnableCloudWatchLogs: this.onEnableCloudWatchLogs,
        onAddCloudWatchLogsInput: this.onAddCloudWatchLogsInput,
        onRemoveCloudWatchLogsInput: this.onRemoveCloudWatchLogsInput,
        onUpdateCloudWatchLogsInputGroupField: this.onUpdateCloudWatchLogsInputGroupField,
        onUpdateCloudWatchLogsInputRegionSelection: this.onUpdateCloudWatchLogsInputRegionSelection,
        onSubmitCloudWatchLogsInputForm: this.onSubmitCloudWatchLogsInputForm,
        onSubmitAccountDelete: this.onSubmitAccountDelete,
      },
    };
    return (
      <AwsConfiguration
        {...props}
      />
    );
  }
}

const mapStateToProps = state => ({
  // AWS Configuration
  allRegions: state.awsConfigurationReducer.allRegions,
  isFetchingLogsInput: state.awsConfigurationReducer.isFetchingLogsInput,
  isFetchingRegions: state.awsConfigurationReducer.isFetchingRegions,
  isFetchingInput: state.awsConfigurationReducer.isFetchingInput,
  isFetchingAccountInfo: state.awsConfigurationReducer.isFetchingAccountInfo,
  initialInputName: state.awsConfigurationReducer.initialInputName,
  initialName: state.awsConfigurationReducer.initialName,
  initialAccountRegionCategory: state.awsConfigurationReducer.initialAccountRegionCategory,
  initialKeyId: state.awsConfigurationReducer.initialKeyId,
  initialSelectedRegions: state.awsConfigurationReducer.initialSelectedRegions,
  initialSelectedEntityTypes: state.awsConfigurationReducer.initialSelectedEntityTypes,
  initialSelectedDimensions: state.awsConfigurationReducer.initialSelectedDimensions,
  inputModifyState: state.awsConfigurationReducer.inputModifyState,
  awsAccountStatus: state.awsConfigurationReducer.awsAccountStatus,
  initialLogInputsInfo: state.awsConfigurationReducer.initialLogInputsInfo,
  cloudWatchLogsInputBulkEditState: state.awsConfigurationReducer.cloudWatchLogsInputBulkEditState,
  cloudWatchLogsInputModifyResult: state.awsConfigurationReducer.cloudWatchLogsInputModifyResult,
  accountDeletionState: state.awsConfigurationReducer.accountDeletionState,
  // Dimension MultiSelect
  selectedDimensions: state.dimensionMultiselectReducer.selectedDimensions,
  // Configuration Status
  connectedCount: state.configurationStatusReducer.connectedCount,
  newCount: state.configurationStatusReducer.newCount,
  initialFetchDone: state.configurationStatusReducer.initialFetchDone,
});

const mapDispatchToProps = dispatch => bindActionCreators({
  onCustomDimensionChange: updateSelectedDimensions,
  onCreateCloudWatchInput: createCloudWatchInput,
  onUpdateCloudWatchInput: updateCloudWatchInput,
  onLoadCloudWatchRegions: loadCloudWatchRegions,
  onLoadAllCloudWatchInputs: loadAllCloudWatchInputs,
  onLoadAllCloudWatchLogsInputs: loadAllCloudWatchLogsInputs,
  onLoadFetchAccountInfo: loadAwsAccountInfo,
  onBulkEditCloudWatchLogsInput: bulkEditCloudWatchLogsInput,
  onAccountDeletionConfirm: accountDeletionConfirm,
  onAccountDeletionReady: accountDeletionReady,
  handleCount,
  onCreateAwsAccount,
  onUpdateAwsAccount,
  onDeleteAwsAccount,
}, dispatch);

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(AwsConfigurationContainer);



