import { createRESTURL } from '@splunk/splunk-utils/url';
import { _ } from '@splunk/ui-utils/i18n';
import EMUtils from '../libs/EMUtils';
import { requestAPI } from './util';

const baseUrl = EMUtils.getAppSplunkdUrl('data/inputs/http');
const globalBaseUrl = createRESTURL('data/inputs/http', { owner: '-', app: '-' });

/**
 * Generates request to get HTTP input settings
 *
 * @returns {Promise} - the Promise object
 */
export function getHttpInputXhr() {
  return requestAPI(
    globalBaseUrl,
    'http',
    'GET',
    { output_mode: 'json' },
    _('Failed to get HTTP Input settings'),
  );
}

/**
 * Generates request to get all HEC tokens
 *
 * @returns {Promise} - the Promise object
 */
export function getEntityMonitoringHecTokensXhr() {
  return requestAPI(
    globalBaseUrl,
    encodeURIComponent('http://splunk_app_infrastructure'),
    'GET',
    { output_mode: 'json' },
    _('Failed to fetch existing HEC tokens'),
  );
}

/**
 * Generates request to create a new HEC token
 *
 * @param {any} tokenName - the HEC token name
 * @param {any} sourcetype - the sourcetype for the endpoint
 * @param {any} index - the index used by the endpoint
 * @returns {Promise} - the Promise object
 */
export function createNewTokenXhr(tokenName, sourcetype, index) {
  return requestAPI(
    baseUrl,
    '',
    'POST',
    {
      name: tokenName,
      index,
      sourcetype,
      output_mode: 'json',
    },
    _('Failed to create HEC token'),
  );
}

/**
 * Generates request to update a existing HEC token
 *
 * @param {any} tokenName - the HEC token name
 * @param {any} sourcetype - the sourcetype for the endpoint
 * @param {any} index - the index used by the endpoint
 * @returns {Promise} - the Promise object
 */
export function updateExistingTokenXhr(tokenName, sourcetype, index) {
  return requestAPI(
    baseUrl,
    encodeURIComponent(tokenName),
    'POST',
    {
      index,
      sourcetype,
      disabled: '0',
      output_mode: 'json',
    },
    _('Failed to update existing token information'),
  );
}

/**
 * Generates request to enable HEC
 *
 * @export
 * @returns (promise) - the Promise object
 */
export function enableHec() {
  return requestAPI(
    baseUrl,
    'http/enable',
    'POST',
    { output_mode: 'json' },
    _('Failed to enable HEC.'),
  );
}

const confBaseUrl = EMUtils.getAppSplunkdUrl('configs');

/**
 * Generates request to fetch the collector URL
 *
 * @export
 * @param {string} [collector='collectd'] - the collector type
 * @returns (promise) - the Promise object
 */
export function getCollectorUrl(collector = 'collectd') {
  return requestAPI(
    confBaseUrl,
    `conf-collectors/${collector}`,
    'GET',
    {
      output_mode: 'json',
      count: -1,
    },
    _('Unable to fetch collector URL.'),
  );
}



