import { createRESTURL } from '@splunk/splunk-utils/url';
import { intersection, forEach, concat, mapValues } from 'lodash';
import { requestAPI } from './util';


const baseUrlConfig = {
  owner: 'nobody',
  app: 'Splunk_TA_aws',
};

const CLOUDWATCH_INPUT_URL = createRESTURL('splunk_ta_aws_aws_cloudwatch', baseUrlConfig);
const ACCOUNT_URL = createRESTURL('splunk_ta_aws/settings/account', baseUrlConfig);
const REGION_BASE_URL = createRESTURL('splunk_ta_aws/splunk_ta_aws_regions', baseUrlConfig);
const CLOUDWATCH_LOG_INPUT_URL = createRESTURL('splunk_ta_aws_aws_cloudwatch_logs', baseUrlConfig);

const CLOUDWATCH_DEFAULT_METRIC_INDEX = 'em_metrics';
const CLOUDWATCH_DEFAULT_PERIOD = 300;
const CLOUDWATCH_DEFAULT_POLLING_INTERVAL = 600;
const CLOUDWATCH_DEFAULT_METRIC_NAMESPACE = ['AWS/EBS', 'AWS/EC2', 'AWS/ELB'];
const CLOUDWATCH_DEFAULT_STATISTICS = ['Average', 'Sum', 'SampleCount', 'Maximum', 'Minimum'];
const CLOUDWATCH_DEFAULT_METRIC_NAMES = '.*';
const CLOUDWATCH_DEFAULT_DIMENSIONS = {
  'AWS/EBS': [
    {
      VolumeId: ['.*'],
    },
  ],
  'AWS/EC2': [
    {
      AutoScalingGroupName: ['.*'],
    },
    {
      ImageId: ['.*'],
    },
    {
      InstanceId: ['.*'],
    },
    {
      InstanceType: ['.*'],
    },
  ],
  'AWS/ELB': [
    {
      AvailabilityZone: ['.*'],
      LoadBalancerName: ['.*'],
    },
    {
      AvailabilityZone: ['.*'],
    },
    {
      LoadBalancerName: ['.*'],
    },
  ],
};

/**
 * The index you want to put data in.
 */
const CLOUDWATCH_LOGS_DEFAULT_INDEX = 'main';
/**
 * Number of seconds, recommended to be 1800.
 * Each time the modular input will query the CloudWatch Logs events
 * no later than <delay> seconds before now.
 * This is to assure your log events have already been ingested by CloudWatch Logs
 * when the modular input queries them.
 */
const CLOUDWATCH_LOGS_DEFAULT_DELAY = 1800;
/**
 * Number of seconds, recommended to be 600. The modular input will make a query once per interval.
 */
const CLOUDWATCH_LOGS_DEFAULT_INTERVAL = 600;
/**
 * GMT time string in '%Y-%m-%dT%H:%M:%S' format.
 * If set, only events after <only_after> will be queried and indexed.
 */
const CLOUDWATCH_LOGS_DEFAULT_ONLY_AFTER = '1970-01-01T00:00:00';
/**
 * The sourcetype you want to put data in.
 */
const CLOUDWATCH_LOGS_DEFAULT_SOURCETYPE = 'aws:cloudwatchlogs';
/**
 * REGEX to strictly match stream names. Default to .*
 */
const CLOUDWATCH_LOGS_DEFAULT_STREAM_MATCHER = '.*';

/**
 * Destructure action and POST to requestAPI with account data
 *
 * @param action with attributes
 * @returns - Ajax promise
 */
export const createAwsAccount = (action) => {
  const { name, key_id, secret_key, category } = action;
  return requestAPI(ACCOUNT_URL, '', 'POST', {
    name,
    key_id,
    secret_key,
    category,
  });
};

/**
 * Destructure action and POST to requestAPI with account data
 * to update account info. Note ta_aws does not accept name as a parameter.
 * @param action with attributes
 * @returns - Ajax promise
 */
export const updateAwsAccount = (action) => {
  const { name, key_id, secret_key, category } = action;
  return requestAPI(ACCOUNT_URL, name, 'POST', {
    key_id,
    secret_key,
    category,
  });
};

/**
 * Destructure action and send DELETE to requestAPI with account data
 * to delete account info.
 * @param action with attributes
 * @returns - Ajax promise
 */
export const deleteAwsAccount = (action) => {
  const { name } = action;
  return requestAPI(ACCOUNT_URL, name, 'DELETE');
};

/**
 * Perform GET on aws account endpoint
 *
 * @returns - Ajax promise
 */
export const retrieveAwsAccountInfo = () => requestAPI(ACCOUNT_URL, '', 'GET');

export const loadCloudWatchRegions = (awsAccount, awsService = 'aws_cloudwatch') => {
  const data = awsAccount === '' ?
    { aws_service: awsService } :
    {
      aws_service: awsService,
      aws_account: awsAccount,
    };
  return requestAPI(REGION_BASE_URL, '', 'GET', data);
};

function buildCloudWatchInputParams(selectedNamespaces) {
  let metricNamespace = [];
  let metricDimensions = [];
  let metricNames = [];
  let statistics = [];
  forEach(intersection(selectedNamespaces, CLOUDWATCH_DEFAULT_METRIC_NAMESPACE), (namespace) => {
    forEach(CLOUDWATCH_DEFAULT_DIMENSIONS[namespace], (dim) => {
      metricNamespace = concat(metricNamespace, namespace);
      metricDimensions = concat(metricDimensions, JSON.stringify([dim]));
      metricNames = concat(metricNames, JSON.stringify(CLOUDWATCH_DEFAULT_METRIC_NAMES));
      statistics = concat(statistics, JSON.stringify(CLOUDWATCH_DEFAULT_STATISTICS));
    });
  });
  return mapValues({
    metricNamespace,
    metricDimensions,
    metricNames,
    statistics,
  }, val => JSON.stringify(val));
}

export const addCloudWatchInput = (data) => {
  const { name, accountName, awsRegions, selectedNamespaces } = data;
  const { metricNamespace, metricDimensions, metricNames, statistics } =
    buildCloudWatchInputParams(selectedNamespaces);
  return requestAPI(CLOUDWATCH_INPUT_URL, '', 'POST', {
    name,
    aws_account: accountName,
    aws_region: awsRegions,
    metric_dimensions: metricDimensions,
    metric_namespace: metricNamespace,
    metric_names: metricNames,
    statistics,
    period: CLOUDWATCH_DEFAULT_PERIOD,
    polling_interval: CLOUDWATCH_DEFAULT_POLLING_INTERVAL,
    index: CLOUDWATCH_DEFAULT_METRIC_INDEX,
    use_metric_format: true,
  });
};

// TODO: do not actually need to pass all data for update
export const updateCloudWatchInput = (data) => {
  const { name, accountName, awsRegions, selectedNamespaces } = data;
  const { metricNamespace, metricDimensions, metricNames, statistics } =
    buildCloudWatchInputParams(selectedNamespaces);
  return requestAPI(CLOUDWATCH_INPUT_URL, name, 'POST', {
    aws_account: accountName,
    aws_region: awsRegions,
    metric_dimensions: metricDimensions,
    metric_namespace: metricNamespace,
    metric_names: metricNames,
    statistics,
    period: CLOUDWATCH_DEFAULT_PERIOD,
    polling_interval: CLOUDWATCH_DEFAULT_POLLING_INTERVAL,
    index: CLOUDWATCH_DEFAULT_METRIC_INDEX,
    use_metric_format: true,
  });
};
export const loadAllCloudWatchInputs = () => requestAPI(CLOUDWATCH_INPUT_URL, '', 'GET', { count: -1 });
export const loadCloudWatchInput = inputName => requestAPI(CLOUDWATCH_INPUT_URL, inputName, 'GET');

export const addCloudWatchLogsInput = (data) => {
  const { name, account, region, group } = data;
  return requestAPI(CLOUDWATCH_LOG_INPUT_URL, '', 'POST', {
    name,
    account,
    region,
    groups: group,
    stream_matcher: CLOUDWATCH_LOGS_DEFAULT_STREAM_MATCHER,
    only_after: CLOUDWATCH_LOGS_DEFAULT_ONLY_AFTER,
    index: CLOUDWATCH_LOGS_DEFAULT_INDEX,
    delay: CLOUDWATCH_LOGS_DEFAULT_DELAY,
    interval: CLOUDWATCH_LOGS_DEFAULT_INTERVAL,
    sourcetype: CLOUDWATCH_LOGS_DEFAULT_SOURCETYPE,
  });
};
export const updateCloudWatchLogsInput = (data) => {
  const { name, account, region, group } = data;
  return requestAPI(CLOUDWATCH_LOG_INPUT_URL, name, 'POST', {
    account,
    region,
    groups: group,
    stream_matcher: CLOUDWATCH_LOGS_DEFAULT_STREAM_MATCHER,
    only_after: CLOUDWATCH_LOGS_DEFAULT_ONLY_AFTER,
    index: CLOUDWATCH_LOGS_DEFAULT_INDEX,
    delay: CLOUDWATCH_LOGS_DEFAULT_DELAY,
    interval: CLOUDWATCH_LOGS_DEFAULT_INTERVAL,
    sourcetype: CLOUDWATCH_LOGS_DEFAULT_SOURCETYPE,
  });
};
export const deleteCloudWatchLogsInput = (data) => {
  const { name } = data;
  return requestAPI(CLOUDWATCH_LOG_INPUT_URL, name, 'DELETE');
};
export const loadAllCloudWatchLogsInput = () => requestAPI(CLOUDWATCH_LOG_INPUT_URL, '', 'GET', { count: -1 });
export const loadCloudWatchLogsInput = inputName => requestAPI(CLOUDWATCH_LOG_INPUT_URL, inputName, 'GET');



