import { uniqBy, cloneDeep, intersectionBy } from 'lodash';
import * as actions from '../../actions/actionTypes';
import { DIMENSION_RETRIEVE_TRACKER } from '../../actions/entities/DimensionRetrieve';
import { FETCH_OBJECT_STATE } from '../../util/StateConstants';

const defaultState = {
  allDimensions: [],
  allDimensionNames: [],
  filteredDimensions: [],
  filteredDimensionNames: [],
  selectedDimensions: [],
  dimensionsFetchState: FETCH_OBJECT_STATE.READY,
};

const dimensionMultiselectReducer = (state = defaultState, action) => {
  switch (action.type) {
    case DIMENSION_RETRIEVE_TRACKER.START: {
      return { ...state, dimensionsFetchState: FETCH_OBJECT_STATE.IN_PROGRESS };
    }

    case DIMENSION_RETRIEVE_TRACKER.SUCCESS: {
      const allDimensions = action.response;
      const allDimensionNames = uniqBy(allDimensions, dim => dim[0]).map(dim => dim[0]);

      return {
        ...state,
        allDimensions,
        allDimensionNames,
        filteredDimensions: cloneDeep(allDimensions),
        filteredDimensionNames: cloneDeep(allDimensionNames),
        dimensionsFetchState: FETCH_OBJECT_STATE.SUCCESS,
      };
    }

    case actions.FETCH_WITH_OPTIONS: {
      if (action.fetchOptions && action.fetchOptions.selectedDimensions) {
        const selectedDimensions = action.fetchOptions.selectedDimensions;
        return { ...state, selectedDimensions };
      }
      return { ...state };
    }

    case DIMENSION_RETRIEVE_TRACKER.FAILURE:
      // TODO: Handle error message by modal/message depends on UX
      return { ...state,
        dimensionsFetchState: FETCH_OBJECT_STATE.ERROR,
      };

    case actions.FILTER_DIMENSIONS: {
      const filter = action.filter;
      const dimensionName = action.dimensionName;

      let filteredDimensions = cloneDeep(state.allDimensions);
      if (dimensionName) {
        filteredDimensions = filteredDimensions.filter(dim =>
          dim[0] === dimensionName && dim[1].toString().startsWith(filter));
      } else {
        filteredDimensions = filteredDimensions.filter(x =>
          filter === '' || x[0].startsWith(filter) || x[1].toString().startsWith(filter));
      }
      const filteredDimensionNames = state.allDimensionNames.filter(x => filter === '' || x.startsWith(filter));
      return {
        ...state,
        filteredDimensions,
        filteredDimensionNames,
      };
    }

    case actions.UPDATE_SELECTED_DIMENSIONS: {
      const selectedDimensions = action.selectedDimensions;
      return { ...state, selectedDimensions };
    }

    case actions.ADD_DIMENSION_SELECTION: {
      const selectedDimension = [];
      selectedDimension.push([`${action.dimension.type}`, `${action.dimension.name}`]);

      // Check to see if currently selectedDimensions exists in state
      const intersections = intersectionBy(
        selectedDimension,
        state.selectedDimensions,
        val => `${val[0]}:${val[1]}`,
      );

      const selectedDimensions = cloneDeep(state.selectedDimensions);
      if (intersections.length === 0) {
        selectedDimensions.push(...selectedDimension);
      }

      return { ...state, selectedDimensions };
    }

    default:
      return state;
  }
};

export default dimensionMultiselectReducer;



