import {
  CLOUDWATCH_INPUT_RETRIEVE,
  START,
  SUCCESS,
  FAILURE,
} from '../../actionTypes';
import ActionUtils from '../../actionUtils';

// This part will be used for saga
export const CLOUDWATCH_INPUT_RETRIEVE_TRACKER =
  ActionUtils.createRequestTypes(CLOUDWATCH_INPUT_RETRIEVE);

export const cloudWatchInputsRetrieveTracker = {
  start: () => ActionUtils.createAction(CLOUDWATCH_INPUT_RETRIEVE_TRACKER[START], {}),
  success: response => ActionUtils.createAction(CLOUDWATCH_INPUT_RETRIEVE_TRACKER[SUCCESS],
    { response }),
  failure: error => ActionUtils.createAction(CLOUDWATCH_INPUT_RETRIEVE_TRACKER[FAILURE],
    { error }),
};

export const loadAllCloudWatchInputs = () => ({
  type: CLOUDWATCH_INPUT_RETRIEVE,
});

export const loadCloudWatchInput = inputName => ({
  type: CLOUDWATCH_INPUT_RETRIEVE,
  inputName,
});



