import React from 'react';
import PropTypes from 'prop-types';

import { _ } from '@splunk/ui-utils/i18n';

import P from '@splunk/react-ui/Paragraph';
import ControlGroup from '@splunk/react-ui/ControlGroup';
import Text from '@splunk/react-ui/Text';
import Switch from '@splunk/react-ui/Switch';
import Button from '@splunk/react-ui/Button';
import Select from '@splunk/react-ui/Select';
import Multiselect from '@splunk/react-ui/Multiselect';
import WaitSpinner from '@splunk/react-ui/WaitSpinner';
import Message from '@splunk/react-ui/Message';
import FormRows from '@splunk/react-ui/FormRows';
import { each, filter } from 'lodash';

import CustomList from '../../common/CustomList/CustomList';
import { createTestHook } from '../../../util/TestSupport';
import ConfigurationStatus from '../../page_contents/configure_components/ConfigurationStatus';
import ConfirmationDialog from '../../common/ConfirmationDialog';
import {
  INPUT_MODIFY,
  AWS_ACCOUNT_STATUS,
  BULK_EDIT_STATE,
  FETCH_OBJECT_STATE,
  DELETE_OBJECT_STATE,
} from '../../../util/StateConstants';


export default class AwsConfiguration extends React.Component {
  static propTypes = {
    /**
     * boolean indicating if an account exists or not
     */
    isNewAccount: PropTypes.bool,
    /**
     * boolean indicating if an input exists or not
     */
    isNewInput: PropTypes.bool,
    /**
     * boolean indicating if a log input exists or not
     */
    isNewLogInput: PropTypes.bool,
    /**
     * aws account name
     */
    name: PropTypes.string,
    /**
     * aws account key id
     */
    keyId: PropTypes.string,
    /**
     * aws account secret key
     */
    secretKey: PropTypes.string,
    /**
     * all regions available for selection
     */
    accountRegionCategory: PropTypes.string,
    /**
     * callback when account form is updated
     */
    onUpdateAccountForm: PropTypes.func,
    /**
     * callback when submit account form
     */
    onSubmitAwsAccountChanges: PropTypes.func,
    /**
     * selected regions specified by existing cloudwatch input
     */
    selectedRegions: PropTypes.array,
    /**
     * callback when selected regions are updated
     */
    onUpdateSelectedRegions: PropTypes.func,
    /**
     * selected cloudwatch namespace specified by existing cloudwatch input
     */
    selectedEntityTypes: PropTypes.array,
    /**
     * callback when selected entity types are updated
     */
    onUpdateSelectedEntityTypes: PropTypes.func,
    /**
     * callback when submit cloudwatch input form
     */
    onSubmitCloudWatchInputForm: PropTypes.func,
    /**
     * all regions available for selection
     */
    allRegions: PropTypes.array,
    /**
     * flag that indicates if regions are being fetched
     */
    isFetchingRegions: PropTypes.bool,
    /**
     * fetch state for aws account info
     */
    isFetchingAccountInfo: PropTypes.oneOf(Object.values(FETCH_OBJECT_STATE)),
    /**
     * create/update state for input
     */
    inputModifyState: PropTypes.oneOf(Object.values(INPUT_MODIFY)),
    /**
     * create/update state for aws account
     */
    awsAccountStatus: PropTypes.oneOf(Object.values(AWS_ACCOUNT_STATUS)),
    /**
     * fetch state for cloudwatch inputs
     */
    isFetchingInput: PropTypes.oneOf(Object.values(FETCH_OBJECT_STATE)),
    /**
     * flag indicating if cloudwatch logs is used
     */
    useCloudWatchLogs: PropTypes.bool,
    /**
     * callback to toggle the cloudwatch logs check box
     */
    onEnableCloudWatchLogs: PropTypes.func,
    /**
     * callback when 'Add' new cloudwatch logs is clicked
     */
    onAddCloudWatchLogsInput: PropTypes.func,
    /**
     * callback when 'x' against an added cloudwatch log input is clicked
     */
    onRemoveCloudWatchLogsInput: PropTypes.func,
    /**
     * callback when group of a cloudwatch log input is updated
     */
    onUpdateCloudWatchLogsInputGroupField: PropTypes.func,
    /**
     * callback when region of a cloudwatch log input is updated
     */
    onUpdateCloudWatchLogsInputRegionSelection: PropTypes.func,
    /**
     * callback when cloudwatch logs input is enabled/ disabled and changes are made to it.
     */
    onSubmitCloudWatchLogsInputForm: PropTypes.func,
    /**
     * state of editing cloudwatch log inputs
     */
    cloudWatchLogsInputBulkEditState: PropTypes.oneOf(Object.values(BULK_EDIT_STATE)),
    /**
     * response when bulk editing cloudwatch log inputs is complete
     */
    cloudWatchLogsInputModifyResult: PropTypes.object,
    /**
     * log groups information
     * format: [{name: 'input1', region: 'us-east-1', group: 'group1'}]
     */
    logInputsInfo: PropTypes.array,
    /**
     * props used for ConfigurationStatus
     */
    connectedCount: PropTypes.number,
    newCount: PropTypes.number,
    initialFetchDone: PropTypes.bool,
    handleCount: PropTypes.func,
    /**
     * fetch status for cloudwatch log inputs
     */
    isFetchingLogsInput: PropTypes.oneOf(Object.values(FETCH_OBJECT_STATE)),
    /**
     * deletion status for aws account
     */
    accountDeletionState: PropTypes.oneOf(Object.values(DELETE_OBJECT_STATE)),
    /**
     * Callback when account deletion completed/ failed.
     */
    onAccountDeletionReady: PropTypes.func,
    /**
     * Callback when account deletion is confirmed.
     */
    onAccountDeletionConfirm: PropTypes.func,
    /**
     * Callback when Delete is clicked on the confirmationDialog
     */
    onSubmitAccountDelete: PropTypes.func,
  };

  static defaultProps = {
    isNewAccount: true,
    isNewInput: true,
    isNewLogInput: true,
    name: '',
    keyId: '',
    secretKey: '',
    onUpdateAccountForm: null,
    onSubmitAwsAccountChanges: null,
    selectedRegions: [],
    onUpdateSelectedRegions: null,
    selectedEntityTypes: [],
    onUpdateSelectedEntityTypes: null,
    accountRegionCategory: '1',
    onSubmitCloudWatchInputForm: null,
    allRegions: [],
    isFetchingRegions: false,
    isFetchingAccountInfo: FETCH_OBJECT_STATE.READY,
    inputModifyState: INPUT_MODIFY.READY,
    awsAccountStatus: AWS_ACCOUNT_STATUS.READY,
    isFetchingInput: FETCH_OBJECT_STATE.READY,
    isFetchingLogsInput: FETCH_OBJECT_STATE.READY,
    accountDeletionState: DELETE_OBJECT_STATE.READY,
    filteredDimensions: [],
    filteredDimensionNames: [],
    useCloudWatchLogs: false,
    logInputsInfo: [],
    onEnableCloudWatchLogs: null,
    onAddCloudWatchLogsInput: null,
    onRemoveCloudWatchLogsInput: null,
    onUpdateCloudWatchLogsInputGroupField: null,
    onUpdateCloudWatchLogsInputRegionSelection: null,
    onSubmitCloudWatchLogsInputForm: null,
    cloudWatchLogsInputBulkEditState: BULK_EDIT_STATE.READY,
    cloudWatchLogsInputModifyResult: {},
    connectedCount: 0,
    newCount: 0,
    initialFetchDone: false,
    handleCount: null,
    onAccountDeletionReady: null,
    onAccountDeletionConfirm: null,
    onSubmitAccountDelete: null,
  };

  constructor(props) {
    super(props);
    this.styles = {
      main: {
        paddingTop: '15px',
      },
      title: {
        fontSize: 14,
        fontWeight: 600,
      },
      loadingText: {
        fontSize: 12,
        fontStyle: 'italic',
        verticalAlign: 'middle',
      },
      loadSpinner: {
        paddingRight: '5px',
        marginLeft: '10px',
        verticalAlign: 'middle',
      },
      disabledSection: {
        pointerEvents: 'none',
        opacity: 0.5,
      },
    };
    this.state = {
      accountErrorStates: {
        name: false,
        keyId: false,
        secretKey: false,
        accountRegionCategory: false,
      },
    };
  }

  getFormattedWaitSpinner = waitSpinnerText => (
    <span>
      <WaitSpinner size="medium" style={this.styles.loadSpinner} />
      <span style={this.styles.loadingText} className="loading-text">
        {_(waitSpinnerText)}
      </span>
    </span>
  )
  getInputModifyStatus = () => {
    const inputModifyState = this.props.inputModifyState;
    const logsInputBulkEditState = this.props.cloudWatchLogsInputBulkEditState;
    const isNewDataSource = this.props.isNewInput && this.props.isNewLogInput;

    if (inputModifyState === INPUT_MODIFY.IN_PROGRESS ||
        logsInputBulkEditState === BULK_EDIT_STATE.IN_PROGRESS) {
      return this.getFormattedWaitSpinner(
        'Setting up AWS data collection - this could take up to 2 minutes');
    }
    const failedLogInputs = filter(
      this.props.cloudWatchLogsInputModifyResult,
      input => input.status.includes('FAILURE'),
    );
    if (failedLogInputs.length > 0 || inputModifyState === INPUT_MODIFY.ERROR) {
      return (
        <Message
          type="error"
          style={{ marginLeft: '135px', marginBottom: '0px' }}
          data-test="input-error-message"
        >
          {isNewDataSource ? _('Error: Could not create input.') : _('Error: Could not update input.')}
        </Message>
      );
    }
    if ((failedLogInputs.length === 0
      && inputModifyState === INPUT_MODIFY.SUCCESS)
      && !this.props.isNewAccount) {
      return (
        <Message
          type="success"
          style={{ marginLeft: '135px', marginBottom: '0px' }}
          data-test="input-success-message"
        >
          {_('Success')}
        </Message>
      );
    }
    return null;
  }

  getAwsAccountStatus = (accountState) => {
    let statusIndicator;
    switch (accountState) {
      case AWS_ACCOUNT_STATUS.IN_PROGRESS:
        statusIndicator = this.getFormattedWaitSpinner('Connecting to AWS Account, Please Wait');
        break;

      case AWS_ACCOUNT_STATUS.SUCCESS:
        statusIndicator = (
          <Message
            type="success"
            style={{ marginLeft: '135px' }}
            data-test="aws-account-success-message"
          >
            {_('Success')}
          </Message>
        );
        break;

      case AWS_ACCOUNT_STATUS.ERROR:
        statusIndicator = (
          <Message
            type="error"
            style={{ marginLeft: '135px' }}
            data-test="aws-account-error-message"
          >
            {this.props.isNewAccount ? _('Error: Could not create account.') : _('Error: Could not update account.')}
          </Message>
        );
        break;

      default:
        break;
    }
    return statusIndicator;
  }

  getConfirmationDialog = () => {
    let title = '';
    let mainMessage = '';
    let infoMessage = '';
    let message = '';
    let primaryButtonText = '';
    let showCancel = false;
    let hideCloseButton = false;
    switch (this.props.accountDeletionState) {
      case DELETE_OBJECT_STATE.CONFIRM: {
        title = _('Delete Account');
        mainMessage = _(`Are you sure you want to delete ${this.props.name}?`);
        infoMessage = _('All inputs related to this account will also be deleted.');
        message = (
          <div>
            <P>{mainMessage}</P>
            <Message type="info">{infoMessage}</Message>
          </div>
        );
        primaryButtonText = _('Delete');
        showCancel = true;
        break;
      }
      case DELETE_OBJECT_STATE.ERROR: {
        title = _('Deletion Failed');
        message = <P>{_('Failed to delete this account.')}</P>;
        primaryButtonText = _('Try Again');
        showCancel = true;
        break;
      }
      case DELETE_OBJECT_STATE.IN_PROGRESS: {
        title = _('Delete Account');
        message = this.getFormattedWaitSpinner('Deleting the account. Do not refresh this page.');
        primaryButtonText = _('Close');
        hideCloseButton = true;
        break;
      }
      case DELETE_OBJECT_STATE.SUCCESS: {
        title = _('Deletion Successful');
        message = <P>{_('Successfully deleted the account.')}</P>;
        primaryButtonText = _('Close');
        showCancel = false;
        break;
      }
      default:
        break;
    }
    return (
      <ConfirmationDialog
        open={
          this.props.accountDeletionState === DELETE_OBJECT_STATE.CONFIRM ||
          this.props.accountDeletionState === DELETE_OBJECT_STATE.IN_PROGRESS ||
          this.props.accountDeletionState === DELETE_OBJECT_STATE.ERROR ||
          this.props.accountDeletionState === DELETE_OBJECT_STATE.SUCCESS
        }
        title={title}
        primaryButtonText={primaryButtonText}
        className="account-deletion-confirmation-dialog"
        primaryButtonClicked={this.props.accountDeletionState === DELETE_OBJECT_STATE.SUCCESS
          ? () => this.beforeClose()
          : () => this.props.onSubmitAccountDelete()}
        beforeClose={() => this.beforeClose()}
        showCancel={showCancel}
        closeOnClick={false}
        hideCloseButton={hideCloseButton}
        disablePrimaryClick={this.props.accountDeletionState === DELETE_OBJECT_STATE.IN_PROGRESS}
      >
        {message}
      </ConfirmationDialog>
    );
  }

  handleConfirmDelete = () => {
    this.props.onAccountDeletionConfirm();
  };

  beforeClose() {
    this.props.onAccountDeletionReady();
  }

  handleAwsAccountSubmit = () => {
    let allowSubmit = true;
    let newAccountErrorStates = this.state.accountErrorStates;
    each(this.state.accountErrorStates, (value, key) => {
      // Do not allow empty strings, show error state
      if (this.props[key] === '' || this.props[key] === undefined) {
        newAccountErrorStates = { ...newAccountErrorStates, [key]: true };
        allowSubmit = false;
      }
    });

    if (allowSubmit) {
      this.props.onSubmitAwsAccountChanges();
    } else {
      this.setState({ accountErrorStates: newAccountErrorStates });
    }
  };

  handleFormFieldChange = (e, { value, name }) => {
    if (name !== undefined && name in this.props) {
      const newErrorState = { ...this.state.accountErrorStates, [name]: false };
      this.setState({ accountErrorStates: newErrorState });
      this.props.onUpdateAccountForm(name, value);
    }
  };

  handleRegionSelectionChange = (e, { values }) => {
    this.props.onUpdateSelectedRegions(values);
  }

  handleSwitchChange = (e, { value }) => {
    this.props.onUpdateSelectedEntityTypes(value);
  };

  handleCloudWatchLogsInputGroupFieldChange = (e, { value, name }) => {
    const idx = parseInt(name, 10);
    this.props.onUpdateCloudWatchLogsInputGroupField(idx, value);
  }

  handleCloudWatchLogsInputRegionChange = (e, { value, name }) => {
    const idx = parseInt(name, 10);
    this.props.onUpdateCloudWatchLogsInputRegionSelection(idx, value);
  }

  handleCloudWatchLogsInputRemove = (e, { index }) => {
    this.props.onRemoveCloudWatchLogsInput(index);
  }

  handleAwsDataSourceSubmit = () => {
    this.props.onSubmitCloudWatchInputForm();
    this.props.onSubmitCloudWatchLogsInputForm();
  }

  render() {
    const selectedEntityTypes = this.props.selectedEntityTypes;
    const isModifyingInput = (
      this.props.inputModifyState === INPUT_MODIFY.IN_PROGRESS ||
      this.props.cloudWatchLogsInputBulkEditState === BULK_EDIT_STATE.IN_PROGRESS);
    const isNewDataSource = this.props.isNewInput && this.props.isNewLogInput;

    const entityTypesCheckbox = ['EC2', 'EBS', 'ELB'].map(value => (
      <Switch
        key={value}
        value={value}
        selected={selectedEntityTypes.includes(value)}
        onClick={this.handleSwitchChange}
        style={{ width: '45px' }}
      >
        {value}
      </Switch>
    ));

    const regionsMultiSelect = (
      <Multiselect
        values={this.props.selectedRegions}
        onChange={this.handleRegionSelectionChange}
        isLoadingOptions={this.props.isFetchingRegions}
      >
        {this.props.allRegions.map(region => (
          <Multiselect.Option key={region.name} label={region.label} value={region.name} />
        ))}
      </Multiselect>
    );

    const regionSelect = (idx, selectedRegion) => (
      <Select
        value={selectedRegion}
        onChange={this.handleCloudWatchLogsInputRegionChange}
        style={{ width: '45%' }}
        name={`${idx}`}
      >
        {this.props.allRegions.map(region => (
          <Select.Option key={region.name} label={region.label} value={region.name} />
        ))}
      </Select>
    );

    const logInputRows = this.props.logInputsInfo.map((info, idx) => {
      const { name, region, group } = info;
      return (
        <FormRows.Row
          index={idx}
          key={name}
          onRequestRemove={this.handleCloudWatchLogsInputRemove}
        >
          {regionSelect(idx, region)}
          <Text
            placeholder={_('Log Group Name')}
            value={group === undefined ? '' : group}
            name={`${idx}`}
            onChange={this.handleCloudWatchLogsInputGroupFieldChange}
            inline
            style={{ width: '50%' }}
          />
        </FormRows.Row>
      );
    });

    const deleteAccountButton = (
      <Button
        label={_('Delete this account')}
        onClick={this.handleConfirmDelete}
        style={{ marginLeft: '60px' }}
        appearance="pill"
      />
    );

    const confirmationDialog = this.getConfirmationDialog();

    return (
      <div style={this.styles.main} {...createTestHook(__filename, name)}>
        {confirmationDialog}
        <P style={{ marginLeft: '25px' }}>
          {_('Follow the instructions to collect data from your AWS instances')} { !this.props.isNewAccount && deleteAccountButton }
        </P>
        <CustomList>
          <CustomList.Item>
            <P style={this.styles.title}>
              {_('Connect to your AWS account')}
            </P>
            {this.props.isFetchingAccountInfo === FETCH_OBJECT_STATE.SUCCESS ? (
              <div>
                <ControlGroup
                  help={this.state.accountErrorStates.name ? _('Required Field.') : ''}
                  label={_('Name')}
                  error={this.state.accountErrorStates.name}
                >
                  <Text
                    canClear
                    name={'name'}
                    error={this.state.accountErrorStates.name}
                    disabled={!this.props.isNewAccount}
                    value={this.props.name}
                    onChange={this.handleFormFieldChange}
                  />
                </ControlGroup>
                <ControlGroup
                  label={_('Key ID')}
                  error={this.state.accountErrorStates.keyId}
                  help={this.state.accountErrorStates.keyId ? _('Required Field.') : ''}
                >
                  <Text
                    canClear
                    name={'keyId'}
                    error={this.state.accountErrorStates.keyId}
                    value={this.props.keyId}
                    onChange={this.handleFormFieldChange}
                  />
                </ControlGroup>
                <ControlGroup
                  label={_('Secret Key')}
                  error={this.state.accountErrorStates.secretKey}
                  help={this.state.accountErrorStates.secretKey ? _('Required Field.') : ''}
                >
                  <Text
                    canClear
                    name={'secretKey'}
                    error={this.state.accountErrorStates.secretKey}
                    value={this.props.secretKey}
                    onChange={this.handleFormFieldChange}
                  />
                </ControlGroup>
                <ControlGroup
                  label="Region Category"
                  error={this.state.accountErrorStates.accountRegionCategory}
                  help={this.state.accountErrorStates.accountRegionCategory ? _('Required Field.') : ''}
                >
                  <Select
                    name={'accountRegionCategory'}
                    error={this.state.accountErrorStates.accountRegionCategory}
                    value={this.props.accountRegionCategory}
                    onChange={this.handleFormFieldChange}
                  >
                    <Select.Option label="Global" value="1" />
                    <Select.Option label="GovCloud" value="2" />
                    <Select.Option label="China" value="4" />
                  </Select>
                </ControlGroup>
                <Message style={{ marginLeft: '135px' }} type="info">{_('All fields required')}</Message>
                <Button
                  label={this.props.isNewAccount ? _('Add AWS account') : _('Update AWS account')}
                  onClick={this.handleAwsAccountSubmit}
                  style={{ marginLeft: '135px' }}
                />
                { this.getAwsAccountStatus(this.props.awsAccountStatus) }
              </div>
            ) : (
              <div style={{ padding: '10px' }}>
                {this.getFormattedWaitSpinner('Loading AWS Account Information')}
              </div>
            )}
          </CustomList.Item>
          <CustomList.Item>
            <P style={this.styles.title}>
              {_('Collect data from')}
            </P>
            { (this.props.isFetchingInput === FETCH_OBJECT_STATE.SUCCESS &&
              this.props.isFetchingLogsInput === FETCH_OBJECT_STATE.SUCCESS) ? (
                <div style={this.props.isNewAccount ? this.styles.disabledSection : { width: '100%' }}>
                  <ControlGroup label={_('AWS Entity Types')} controlsLayout="none">
                    {entityTypesCheckbox}
                  </ControlGroup>
                  <ControlGroup label={_('AWS Regions')} controlsLayout="none">
                    {regionsMultiSelect}
                  </ControlGroup>
                  <ControlGroup
                    label={_('CloudWatch Logs')}
                    controlsLayout="none"
                    help={
                      <span>
                        {_('When setting up CloudWatch Logs agent configuration in AWS, edit the log stream name (log_stream_name) ' +
                        'with a unique name (instance_id) for each log group within the configuration file. This defines the log ' +
                        'stream’s identity for correlation of logs to individual instances and metric data.')}
                      </span>
                    }
                  >
                    <Switch
                      key="cloudwatch-logs"
                      value={_('Yes')}
                      selected={this.props.useCloudWatchLogs}
                      onClick={this.props.onEnableCloudWatchLogs}
                      style={{ width: '45px' }}
                    >
                      {_('Yes')}
                    </Switch>
                    { this.props.useCloudWatchLogs &&
                      <FormRows
                        addLabel="Add"
                        style={{ width: '100%' }}
                        onRequestAdd={this.props.onAddCloudWatchLogsInput}
                      >
                        {logInputRows}
                      </FormRows>
                    }
                  </ControlGroup>
                  <Button
                    label={isNewDataSource ?
                      _('Add AWS data source') :
                      _(`${this.props.selectedEntityTypes.length === 0 &&
                        !this.props.useCloudWatchLogs ? 'Delete' : 'Update'} AWS data source`)}
                    onClick={this.handleAwsDataSourceSubmit}
                    disabled={isModifyingInput
                      || (isNewDataSource &&
                          this.props.selectedEntityTypes.length === 0 &&
                          !this.props.useCloudWatchLogs)}
                    style={{ marginLeft: '135px' }}
                    data-test="modify-aws-input-btn"
                  />
                  { this.getInputModifyStatus() }
                </div>
              ) : (
                <div style={{ padding: '10px' }}>
                  {this.getFormattedWaitSpinner('Loading AWS Data Source Options')}
                </div>
              )}
          </CustomList.Item>
          <CustomList.Item>
            <P style={this.styles.title}>
              {_('Once your AWS account is added, verify your data connection')}
            </P>
            <ConfigurationStatus
              connectedCount={this.props.connectedCount}
              newCount={this.props.newCount}
              initialFetchDone={this.props.initialFetchDone}
              handleCount={this.props.handleCount}
            />
          </CustomList.Item>
        </CustomList>
      </div>
    );
  }
}



