import _ from 'lodash';
import * as actions from '../../actions/actionTypes';
import {
  CLOUDWATCH_REGIONS_RETRIEVE_TRACKER,
  CLOUDWATCH_INPUT_CREATION_TRACKER,
} from '../../actions/configure/awsConfiguration/CloudWatchInputCreation';
import {
  CLOUDWATCH_INPUT_RETRIEVE_TRACKER,
} from '../../actions/configure/awsConfiguration/CloudWatchInputRetrieve';
import {
  CLOUDWATCH_INPUT_UPDATE_TRACKER,
} from '../../actions/configure/awsConfiguration/CloudWatchInputUpdate';
import {
  CLOUDWATCH_LOGS_INPUT_RETRIEVE_TRACKER,
} from '../../actions/configure/awsConfiguration/CloudWatchLogsInputRetrieve';
import { AWS_ACCOUNT_CREATE_TRACKER } from '../../actions/configure/awsConfiguration/AwsAccountCreation';
import { AWS_ACCOUNT_RETRIEVE_TRACKER } from '../../actions/configure/awsConfiguration/AwsAccountRetrieve';
import { AWS_ACCOUNT_UPDATE_TRACKER } from '../../actions/configure/awsConfiguration/AwsAccountUpdate';
import { AWS_ACCOUNT_DELETE_TRACKER } from '../../actions/configure/awsConfiguration/AwsAccountDeletion';
import {
  INPUT_MODIFY,
  AWS_ACCOUNT_STATUS,
  BULK_EDIT_STATE,
  FETCH_OBJECT_STATE,
  DELETE_OBJECT_STATE,
} from '../../util/StateConstants';
import {
  CLOUDWATCH_LOGS_INPUT_BULK_EDIT_TRACKER,
} from '../../actions/configure/awsConfiguration/CloudWatchLogsInputBulkEdit';
import {
  SOURCETYPES_RETRIEVE_TRACKER,
} from '../../actions/configure/unixConfiguration/SourceTypesRetrieve';

const configurationStatusInitialState = {
  // Configuration Status Entities Count
  connectedCount: 0,
  newCount: 0,
  initialFetchDone: false,
};

const unixConfigurationInitialState = {
  // HEC state
  isFetchingToken: false,
  isHttpInputEnabled: false,
  hecToken: null,
  hecPort: null,
  errorStatusText: null,
  // Collector install package link
  packageLink: '',
  sourceTypes: [],
};

const awsConfigurationInitialState = {
  allRegions: [],
  isFetchingRegions: false,
  isFetchingAccountInfo: FETCH_OBJECT_STATE.READY,
  isFetchingInput: FETCH_OBJECT_STATE.READY,
  isFetchingLogsInput: FETCH_OBJECT_STATE.READY,
  initialInputName: '',
  initialSelectedRegions: [],
  initialSelectedEntityTypes: [],
  initialAccountRegionCategory: '1',
  initialName: '',
  initialKeyId: '',
  initialSelectedDimensions: [],
  inputModifyState: INPUT_MODIFY.READY,
  awsAccountStatus: AWS_ACCOUNT_STATUS.READY,
  initialLogInputsInfo: [],
  cloudWatchLogsInputBulkEditState: BULK_EDIT_STATE.READY,
  cloudWatchLogsInputModifyResult: {},
  accountDeletionState: DELETE_OBJECT_STATE.READY,
};

/**
 * Reducer for configurationStatus
 *
 * @export
 * @param {any} [state=initialState] - the current state
 * @param {any} action - the current action
 * @returns {Object} - the next state
 */
export const configurationStatusReducer = (state = configurationStatusInitialState, action) => {
  const newState = { ...state };
  switch (action.type) {
    case actions.UPDATE_COUNT:
      if (!newState.initialFetchDone) {
        newState.initialFetchDone = true;
        newState.connectedCount = action.count;
      }
      newState.newCount = action.count - newState.connectedCount;
      break;

    default:
      break;
  }
  return newState;
};

/**
 * Reducer for unixConfiguration
 *
 * @export
 * @param {any} [state=initialState] - the current state
 * @param {any} action - the current action
 * @returns {Object} - the next state
 */
export const unixConfigurationReducer = (state = unixConfigurationInitialState, action) => {
  const newState = { ...state };
  const customLogsAndSources = {};
  switch (action.type) {
    case actions.FETCH_HEC_TOKEN:
      switch (action.status) {
        case 'start':
          newState.isFetchingToken = true;
          break;
        case 'error':
          newState.isFetchingToken = false;
          newState.hecToken = null;
          newState.errorStatusText = action.response.statusText;
          break;
        case 'success':
          newState.isFetchingToken = false;
          newState.hecToken = action.response.hecToken;
          break;
        default:
          break;
      }
      break;

    case actions.FETCH_AGENT_URL:
      newState.packageLink = action.agent_url;
      break;

    case actions.UPDATE_CUSTOM_METRICS_AND_LOGS:
      // eslint-disable-next-line no-return-assign
      _.each(action.customLogs, log => (customLogsAndSources[log.path] = log.sourceType));
      newState.selectedMetrics = action.selectedMetrics;
      newState.selectedMetricOptions = action.selectedMetricOptions;
      newState.selectedLogs = { ...action.selectedLogs, ...customLogsAndSources };
      break;

    case actions.ENABLE_HEC:
      switch (action.status) {
        case 'start':
          newState.isFetchingToken = true;
          break;
        case 'error':
          newState.isFetchingToken = false;
          newState.isHttpInputEnabled = false;
          newState.hecPort = null;
          break;
        case 'success':
          newState.isFetchingToken = false;
          newState.isHttpInputEnabled = action.settings.splunkHttpInputEnabled;
          newState.hecPort = action.settings.hecPort;
          break;
        default:
          break;
      }
      break;

    // sourceTypes Retrieve
    case SOURCETYPES_RETRIEVE_TRACKER.SUCCESS:
      newState.sourceTypes = action.response;
      break;

    default:
      break;
  }
  return newState;
};

/**
 * Reducer for awsConfiguration
 *
 * @export
 * @param {any} [state=initialState] - the current state
 * @param {any} action - the current action
 * @returns {Object} - the next state
 */
export const awsConfigurationReducer = (state = awsConfigurationInitialState, action) => {
  const newState = { ...state };
  switch (action.type) {
    // AWS Account Creation
    case AWS_ACCOUNT_CREATE_TRACKER.START:
    case AWS_ACCOUNT_UPDATE_TRACKER.START:
      newState.awsAccountStatus = AWS_ACCOUNT_STATUS.IN_PROGRESS;
      break;

    case AWS_ACCOUNT_CREATE_TRACKER.SUCCESS:
    case AWS_ACCOUNT_UPDATE_TRACKER.SUCCESS:
      newState.awsAccountStatus = AWS_ACCOUNT_STATUS.SUCCESS;
      if (action.response && action.response.name) {
        newState.initialName = action.response.name;
        newState.initialKeyId = action.response.keyId;
        newState.initialAccountRegionCategory = action.response.category;
      }
      break;

    case AWS_ACCOUNT_CREATE_TRACKER.FAILURE:
    case AWS_ACCOUNT_UPDATE_TRACKER.FAILURE:
      newState.awsAccountStatus = AWS_ACCOUNT_STATUS.ERROR;
      break;

    // AWS Account Retrieve
    case AWS_ACCOUNT_RETRIEVE_TRACKER.START:
      newState.isFetchingAccountInfo = FETCH_OBJECT_STATE.IN_PROGRESS;
      break;

    case AWS_ACCOUNT_RETRIEVE_TRACKER.SUCCESS:
      newState.isFetchingAccountInfo = FETCH_OBJECT_STATE.SUCCESS;
      if (action.response && action.response.name) {
        newState.initialName = action.response.name;
        newState.initialKeyId = action.response.keyId;
        newState.initialAccountRegionCategory = action.response.category;
      }
      break;

    case AWS_ACCOUNT_RETRIEVE_TRACKER.FAILURE:
      newState.isFetchingAccountInfo = FETCH_OBJECT_STATE.ERROR;
      break;
    // AWS Region retrieve
    case CLOUDWATCH_REGIONS_RETRIEVE_TRACKER.START:
      newState.isFetchingRegions = true;
      break;

    case CLOUDWATCH_REGIONS_RETRIEVE_TRACKER.SUCCESS:
      newState.isFetchingRegions = false;
      newState.allRegions = action.response;
      break;

    case CLOUDWATCH_REGIONS_RETRIEVE_TRACKER.FAILURE:
      newState.isFetchingRegions = false;
      break;

    // AWS CloudWatch Input Create/Update
    case CLOUDWATCH_INPUT_CREATION_TRACKER.START:
    case CLOUDWATCH_INPUT_UPDATE_TRACKER.START:
      newState.inputModifyState = INPUT_MODIFY.IN_PROGRESS;
      break;

    case CLOUDWATCH_INPUT_CREATION_TRACKER.SUCCESS:
    case CLOUDWATCH_INPUT_UPDATE_TRACKER.SUCCESS:
      if (action.response.length > 0) {
        // TODO: should not hardcoding 0 once we support multiple inputs
        const input = action.response[0];
        newState.initialSelectedRegions = input.awsRegion;
        newState.initialSelectedEntityTypes = input.metricNamespace;
        newState.initialName = input.awsAccount;
        newState.initialSelectedDimensions = input.customDimensions;
        newState.initialInputName = input.inputName;
      } else {
        // Input is deleted
        newState.initialSelectedRegions = [];
        newState.initialSelectedEntityTypes = [];
        newState.initialInputName = '';
        newState.initialSelectedDimensions = [];
      }
      newState.inputModifyState = INPUT_MODIFY.SUCCESS;
      break;

    case CLOUDWATCH_INPUT_CREATION_TRACKER.FAILURE:
    case CLOUDWATCH_INPUT_UPDATE_TRACKER.FAILURE:
      newState.inputModifyState = INPUT_MODIFY.ERROR;
      break;

    // AWS CloudWatch Input Retrieve
    case CLOUDWATCH_INPUT_RETRIEVE_TRACKER.START:
      newState.isFetchingInput = FETCH_OBJECT_STATE.IN_PROGRESS;
      break;

    case CLOUDWATCH_INPUT_RETRIEVE_TRACKER.SUCCESS:
      newState.isFetchingInput = FETCH_OBJECT_STATE.SUCCESS;
      if (action.response.length > 0) {
        // TODO: should not hardcoding 0 once we support multiple inputs
        const input = action.response[0];
        newState.initialSelectedRegions = input.awsRegion;
        newState.initialSelectedEntityTypes = input.metricNamespace;
        newState.initialName = input.awsAccount;
        newState.initialSelectedDimensions = input.customDimensions;
        newState.initialInputName = input.inputName;
      }
      break;

    case CLOUDWATCH_INPUT_RETRIEVE_TRACKER.FAILURE:
      newState.isFetchingInput = FETCH_OBJECT_STATE.ERROR;
      break;

    // AWS CloudWatch Input Logs Retrieve
    case CLOUDWATCH_LOGS_INPUT_RETRIEVE_TRACKER.START:
      newState.isFetchingLogsInput = FETCH_OBJECT_STATE.IN_PROGRESS;
      break;
    case CLOUDWATCH_LOGS_INPUT_RETRIEVE_TRACKER.SUCCESS:
      newState.isFetchingLogsInput = FETCH_OBJECT_STATE.SUCCESS;
      newState.initialLogInputsInfo = action.response;
      break;

    case CLOUDWATCH_LOGS_INPUT_RETRIEVE_TRACKER.FAILURE:
      newState.isFetchingLogsInput = FETCH_OBJECT_STATE.ERROR;
      break;

    // AWS CloudWatch Logs Input Bulk Edit
    case CLOUDWATCH_LOGS_INPUT_BULK_EDIT_TRACKER.BULK_START:
      newState.cloudWatchLogsInputBulkEditState = BULK_EDIT_STATE.IN_PROGRESS;
      break;

    case CLOUDWATCH_LOGS_INPUT_BULK_EDIT_TRACKER.BULK_COMPLETE: {
      newState.cloudWatchLogsInputBulkEditState = BULK_EDIT_STATE.COMPLETE;
      newState.cloudWatchLogsInputModifyResult = action.response;
      let newLogInputs = [];
      _.forEach(action.response, (inputResult) => {
        const input = _.pick(inputResult, ['name', 'region', 'group']);
        const status = inputResult.status;
        if (status === 'CREATE_SUCCESS' || status === 'UPDATE_SUCCESS' || status === 'DELETE_FAILURE') {
          newLogInputs.push(input);
        } else if (status === 'UPDATE_FAILURE') {
          // if failed to update, then put old input data in
          newLogInputs.push(_.find(state.initialLogInputsInfo, { name: input.name }));
        }
      });
      // Add no-op input
      newLogInputs = newLogInputs.concat(
        state.initialLogInputsInfo.filter(input => !_.has(action.response, input.name)));
      newState.initialLogInputsInfo = _.orderBy(newLogInputs, 'name', 'asc');
      break;
    }

    // AWS account deletion states.
    case actions.AWS_ACCOUNT_DELETION_CONFIRM:
      newState.accountDeletionState = DELETE_OBJECT_STATE.CONFIRM;
      break;

    case actions.AWS_ACCOUNT_DELETION_READY:
      newState.accountDeletionState = DELETE_OBJECT_STATE.READY;
      break;

    case AWS_ACCOUNT_DELETE_TRACKER.START:
      newState.accountDeletionState = DELETE_OBJECT_STATE.IN_PROGRESS;
      break;

    case AWS_ACCOUNT_DELETE_TRACKER.SUCCESS:
      newState.accountDeletionState = DELETE_OBJECT_STATE.SUCCESS;
      newState.initialName = '';
      newState.initialKeyId = '';
      break;

    case AWS_ACCOUNT_DELETE_TRACKER.FAILURE:
      newState.accountDeletionState = DELETE_OBJECT_STATE.ERROR;
      break;

    default:
      break;
  }
  return newState;
};



