import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { xor, remove, cloneDeep, isEqual, union, last, includes, find } from 'lodash';
import Multiselect from '@splunk/react-ui/Multiselect';
import { _ } from '@splunk/ui-utils/i18n';
import { createTestHook } from '../../util/TestSupport';
import { UNHEALTHY_FLAT } from './Colors';
import { FETCH_OBJECT_STATE } from '../../util/StateConstants';

/**
 * Multiselect component specifically for metric dimension selection
 * This is a controlled component for all intents and purposes
 *
 * @export
 * @class DimensionMultiSelect
 * @extends {Component}
 */
export default class DimensionMultiSelect extends Component {
  static propTypes = {
    /**
     * Array of dimensions: [[<name>, <value>], ...]
     */
    dimensions: PropTypes.arrayOf(PropTypes.arrayOf(PropTypes.string)),

    /**
     * Array of dimension names
     */
    dimensionNames: PropTypes.arrayOf(PropTypes.string),

    /**
     * The list of dimensions that have already been selected
     */
    selectedDimensions: PropTypes.arrayOf(PropTypes.arrayOf(PropTypes.string)),

    /**
     * Callback for whenever filter text is entered
     *   Arguments:
     *     filterString - keyword filter to be searched for
     *     dimensionNameFilter - apply keyword search only to the given dimension name
     *   Returns: none. The parent is responsible for updating dimensions and dimensionNames
     *     as necessary.
     */
    onFilterChange: PropTypes.func,

    /**
     * Callback for whenever a dimension is selected
     *   Arguments: Array of selected dimensions
     *   Returns: none
     */
    onValueChange: PropTypes.func,

    /**
     * Value to show in box when empty
     */
    placeholder: PropTypes.string,

    /**
     * fetch state for dimension options
     */
    dimensionsFetchState: PropTypes.oneOf(Object.values(FETCH_OBJECT_STATE)),

    /**
     * When true, adds an option to add a new dimension
     */
    allowNewDimensions: PropTypes.bool,

    /**
     * Styling for the underlying Multiselect component
     */
    multiselectStyle: PropTypes.object,

    /**
     * When true, disables the Multiselect component
     */
    disabled: PropTypes.bool,

    /**
     * Show loading animation
     * @type {boolean}
     */
    animateLoading: PropTypes.bool,
  }

  static defaultProps = {
    dimensions: [],
    dimensionNames: [],
    selectedDimensions: [],
    onFilterChange: null,
    onValueChange: null,
    placeholder: _('Filter dimensions'),
    dimensionsFetchState: FETCH_OBJECT_STATE.READY,
    allowNewDimensions: false,
    multiselectStyle: {},
    disabled: false,
    animateLoading: true,
  }

  /**
   * A value to indicate that the option is a dimension name
   *
   * @static
   *
   * @memberOf DimensionMultiSelect
   */
  static dimensionNameSentinel = '<dimension_name>'

  /**
   * Creates an instance of DimensionMultiSelect.
   *
   * @memberOf DimensionMultiSelect
   */
  constructor(props, ...rest) {
    super(props, ...rest);

    this.errorStates = {
      dimensionNameWildcard: _('Wildcard searches not supported for dimension names.'),
      onlyOneWildcard: _('Only one wildcard character allowed.'),
      wildcardOnlyAtEnd: _('Wildcard can only be at the end.'),
      unknownDimensionName: _('Unknown dimension name.'),
      noPrefixDimNameUnderscore: _('Cannot prefix a dimension name with a "_"'),
      alphanumericOnly: _('Only alphanumeric characters and \'-\', \'_\', \'.\' are allowed.'),
    };

    this.state = {
      dimensionNameFilter: null,
      displayValues: this.props.selectedDimensions.map(value => `${value[0]}:${value[1]}`) || [],
      errorState: null,
    };

    this.filterString = '';

    this.nonAlphaNumericText = /[^a-zA-Z_.\-\d:=]/g;
    this.invalidDimensionNamePrefix = /^_/;
  }

  /**
   * Runs before the component is rendered
   *
   * @param {any} nextProps - the props about to be processed
   *
   * @memberOf DimensionMultiSelect
   */
  componentWillReceiveProps(nextProps) {
    // Set displayValues every time props have changed and keyword is not being typed
    if (!this.filterString &&
      !isEqual(this.props.selectedDimensions, nextProps.selectedDimensions)
    ) {
      this.setState({
        displayValues: nextProps.selectedDimensions.map(value => `${value[0]}:${value[1]}`),
      });
    }
  }

  /**
   * Generates options for populate the multiselect dropdown
   *
   * @memberOf DimensionMultiSelect
   */
  generateOptions = () => {
    const options = [];
    // All selected dimensions need to be an option that's hidden.
    // During filtering or errors, we don't end up generating an option
    // for an already selected dimension, which causes it to show up weirdly.
    // In that case, we need to generate these hidden ones separately.
    const dimensionsToHide = cloneDeep(this.props.selectedDimensions);
    if (!this.state.errorState) {
      if (this.state.dimensionNameFilter) {
        // Add the new dimension option
        if (this.props.allowNewDimensions && this.filterString &&
          !find(this.props.dimensions, dim => (
            dim[0] === this.state.dimensionNameFilter && dim[1] === this.filterString
          ))) {
          options.push(<Multiselect.Option
            label={`${this.state.dimensionNameFilter}: ${this.filterString}`}
            value={`${this.state.dimensionNameFilter}:${this.filterString}`}
            key={`${this.state.dimensionNameFilter}: ${this.filterString}`}
          />);
        }

        // Add the wildcard option
        if (!this.state.errorState && last(this.filterString) === _('*')) {
          options.push(<Multiselect.Option
            label={`${this.state.dimensionNameFilter}: ${this.filterString}`}
            value={`${this.state.dimensionNameFilter}:${this.filterString}`}
            key={`${this.state.dimensionNameFilter}: ${this.filterString}`}
          />);
        }
      } else if (this.props.dimensionNames.length > 0) {
        options.push(
          <Multiselect.Heading key="Dimension Names">
            {_('Dimension Names')}
          </Multiselect.Heading>);
        options.push(...this.props.dimensionNames.map(dimensionName =>
          (<Multiselect.Option
            label={`${dimensionName}`}
            value={`${dimensionName}:${DimensionMultiSelect.dimensionNameSentinel}`}
            key={`${dimensionName}:`}
          />),
        ));
      }

      if (this.state.dimensionNameFilter ||
        (this.props.dimensions.length > 0 && this.filterString)) {
        options.push(
          <Multiselect.Heading key="Dimension Values">
            {_('Dimension Values')}
          </Multiselect.Heading>);
        options.push(...this.props.dimensions.map((dimension) => {
          const removedDim = remove(
            dimensionsToHide,
            dim => dim[0] === dimension[0] && dim[1] === dimension[1]);
          return (<Multiselect.Option
            label={`${dimension[0]}: ${dimension[1]}`}
            value={`${dimension[0]}:${dimension[1]}`}
            key={`${dimension[0]}:${dimension[1]}`}
            hidden={removedDim.length > 0}
          />);
        }));
      }
    }


    // Add the sentinel as a hidden option so it doesn't
    // show up with "location:<dimension_name>"
    if (this.state.dimensionNameFilter) {
      options.push(
        (<Multiselect.Option
          label={`${this.state.dimensionNameFilter}:`}
          value={`${this.state.dimensionNameFilter}:${DimensionMultiSelect.dimensionNameSentinel}`}
          key={`${this.state.dimensionNameFilter}:${DimensionMultiSelect.dimensionNameSentinel}`}
          hidden
        />),
      );
    }

    // Add all of the remaining selected dimensions as options
    options.push(...dimensionsToHide.map(dimension =>
      (<Multiselect.Option
        label={`${dimension[0]}: ${dimension[1]}`}
        value={`${dimension[0]}:${dimension[1]}`}
        key={`${dimension[0]}:${dimension[1]}`}
        hidden
      />),
    ));

    return options;
  }

  /**
   * Handler for when a value is selected in the multiselect. This calls
   * back onValueChange to allow the parent component to update its
   * internal state. This also calls onFilterChange when dimension names
   * are selected.
   *
   * @memberOf DimensionMultiSelect
   */
  handleValueChange = (e, { values }) => {
    const flattenedSelectedValues = this.props.selectedDimensions.map(value => `${value[0]}:${value[1]}`);
    const changedValue = xor(
      values,
      this.state.displayValues,
    )[0];

    const updatedSelectedValues = cloneDeep(this.props.selectedDimensions);
    // Split by the first ":" to avoid partial dimension selected.
    const changedValueComponents = changedValue.split(/:(.+)/);

    // The code works under the assumption that we always append values to the end of the
    // values selection. Please do not break that assumption.
    if (values.includes(changedValue)) {
      updatedSelectedValues.push(changedValueComponents);
      if (changedValueComponents[1] === DimensionMultiSelect.dimensionNameSentinel) {
        // Dimension name was selected so new filter values need to be fetched
        this.setState({
          dimensionNameFilter: changedValueComponents[0],
          displayValues: updatedSelectedValues.map(value =>
            `${value[0]}:${value[1] || DimensionMultiSelect.dimensionNameSentinel}`),
        });
        this.props.onFilterChange('', changedValueComponents[0]);
      } else if (this.state.dimensionNameFilter) {
        // Dimension value for a specific dimension name was selected.
        // Reset all the filters
        this.setState({
          dimensionNameFilter: null,
        });

        this.props.onFilterChange('', null);
        this.props.onValueChange(updatedSelectedValues);
      } else {
        // Dimension value was selected. Reset filter and update entity table
        this.props.onFilterChange('', null);
        this.props.onValueChange(updatedSelectedValues);
      }
    } else if (flattenedSelectedValues.includes(changedValue)) {
      // Dimension was removed so set the updated selections back and reset filters
      const currentValues = cloneDeep(values);
      remove(updatedSelectedValues, value =>
        value[0] === changedValueComponents[0] &&
        value[1] === changedValueComponents[1]);

      // Callback with the remaning values
      this.props.onValueChange(
        updatedSelectedValues.filter(
          value => value[1] !== DimensionMultiSelect.dimensionNameSentinel),
      );

      const currentDisplayValues = this.state.displayValues;
      this.setState({
        displayValues: union(currentDisplayValues, currentValues),
      });
    } else {
      // Dimension name selection was removed so reset filters and display with currently
      // selected dimensions.
      this.setState({
        dimensionNameFilter: null,
        displayValues: updatedSelectedValues.map(value =>
          `${value[0]}:${value[1] || DimensionMultiSelect.dimensionNameSentinel}`),
      });
      this.props.onFilterChange('', null);
    }

    this.filterString = null;
  }

  /**
   * Handler for any text typed into multiselect. This calls back
   * onFilterChange with the expectation that the parent component
   * updates the dimension and dimensionNames
   *
   * @memberOf DimensionMultiSelect
   */
  handleFilterChange = (e, { keyword }) => {
    let errorState = null;
    let callbackFilter = keyword;
    if (this.props.allowNewDimensions && keyword.match(this.nonAlphaNumericText)) {
      errorState = 'alphanumericOnly';
    } else if (!this.state.dimensionNameFilter && keyword.match(this.invalidDimensionNamePrefix)) {
      errorState = 'noPrefixDimNameUnderscore';
    } else if (keyword.includes(_('*'))) {
      if (!this.state.dimensionNameFilter) {
        // Keyword had a wildcard when a dimension value is not being filtered
        errorState = 'dimensionNameWildcard';
      } else if (keyword.match(/[*]/g).length > 1) {
        // Keyword has more than one wildcard character
        errorState = 'onlyOneWildcard';
      } else if (last(keyword) !== _('*')) {
        // Keyword has characters following the wildcard character
        errorState = 'wildcardOnlyAtEnd';
      } else {
        callbackFilter = keyword.slice(0, -1);
      }
    } else if (!this.state.dimensionNameFilter &&
        (keyword.includes(_(':')) || keyword.includes(_('=')))) {
      // Auto-populates the dimension name if the user hits ':' or '=' and it's
      // a valid dimension name.
      if (!this.props.allowNewDimensions &&
        !includes(this.props.dimensionNames, keyword.slice(0, -1))) {
        errorState = 'unknownDimensionName';
      } else {
        // No other processing should happen after value change is triggered
        this.handleValueChange({}, {
          values: [...this.state.displayValues, `${keyword.slice(0, -1)}:${DimensionMultiSelect.dimensionNameSentinel}`],
        });
        return;
      }
    }

    this.setState({
      errorState,
    });

    if (!errorState) {
      this.props.onFilterChange(callbackFilter, this.state.dimensionNameFilter);
      this.filterString = keyword;
    }
  }

  /**
   * Generates the options to display and renders
   *
   * @memberOf DimensionMultiSelect
   */
  render() {
    const options = this.generateOptions();
    const errorMessage = this.state.errorState ? this.errorStates[this.state.errorState] : '';

    return (
      <Multiselect
        {...createTestHook(__filename, name)}
        style={this.props.multiselectStyle}
        values={this.state.displayValues}
        placeholder={this.props.placeholder}
        onChange={this.handleValueChange}
        onFilterChange={this.handleFilterChange}
        animateLoading={this.props.animateLoading}
        isLoadingOptions={this.props.dimensionsFetchState === FETCH_OBJECT_STATE.IN_PROGRESS}
        error={!!this.state.errorState}
        noOptionsMessage={!!this.state.errorState &&
          <span style={{ color: UNHEALTHY_FLAT }}>{errorMessage}</span>
        }
        controlledFilter
        disabled={this.props.disabled}
        inline
      >
        {options}
      </Multiselect>
    );
  }
}



