import {
  AWS_ACCOUNT_CREATE,
  START,
  SUCCESS,
  FAILURE,
} from '../../actionTypes';
import ActionUtils from '../../actionUtils';

/**
 *
 * @param  {string} name  - The aws account name
 * @param  {string} keyId
 * @param  {string} secretKey
 * @param  {int} regionCategory - The aws region
 * @return {Object} - an action object with the results of the request
 */
export function onCreateAwsAccount(name, keyId, secretKey, regionCategory) {
  return {
    type: AWS_ACCOUNT_CREATE,
    name,
    key_id: keyId,
    secret_key: secretKey,
    category: regionCategory,
  };
}

export const AWS_ACCOUNT_CREATE_TRACKER = ActionUtils.createRequestTypes(AWS_ACCOUNT_CREATE);

/**
* Return action for each status
*/
export const awsCreateAccountTracker = {
  start: () =>
    ActionUtils.createAction(AWS_ACCOUNT_CREATE_TRACKER[START], {}),
  success: response =>
    ActionUtils.createAction(AWS_ACCOUNT_CREATE_TRACKER[SUCCESS], { response }),
  failure: error =>
    ActionUtils.createAction(AWS_ACCOUNT_CREATE_TRACKER[FAILURE], { error }),
};



