import React from 'react';
import PropTypes from 'prop-types';
import { createTestHook } from '../../../util/TestSupport';

export default class CustomListItem extends React.Component {
  static propTypes = {
    children: PropTypes.node,
    itemNumber: PropTypes.number,
  }

  static defaultProps = {
    children: null,
    itemNumber: 0,
  }

  css = {
    display: 'flex',
    marginBottom: '20px',
  }

  beforeCss = {
    marginRight: '15px',
    backgroundColor: '#006d9c',
    color: 'white',
    fontWeight: 'bold',
    textAlign: 'center',
    lineHeight: '24px',
    borderRadius: '100px',
    width: '24px',
    height: '24px',
    flexShrink: 0,
  }

  render() {
    return (
      <li style={this.css} {...createTestHook(__filename, name)}>
        <span style={this.beforeCss}>{this.props.itemNumber}</span>
        <div style={{ paddingTop: '3px', width: '100%' }}>
          {this.props.children}
        </div>
      </li>
    );
  }
}



