import {
  CLOUDWATCH_LOGS_INPUT_BULK_EDIT,
  BULK_START,
  BULK_COMPLETE,
} from '../../actionTypes';
import ActionUtils from '../../actionUtils';

export const CLOUDWATCH_LOGS_INPUT_BULK_EDIT_TRACKER =
    ActionUtils.createBulkRequestType(CLOUDWATCH_LOGS_INPUT_BULK_EDIT);

export const cloudWatchLogsInputBulkEditTracker = {
  start: () => ActionUtils.createAction(
    CLOUDWATCH_LOGS_INPUT_BULK_EDIT_TRACKER[BULK_START],
    {}),
  complete: response => ActionUtils.createAction(
    CLOUDWATCH_LOGS_INPUT_BULK_EDIT_TRACKER[BULK_COMPLETE],
    { response }),
};

export const bulkEditCloudWatchLogsInput =
  ({ account, useCloudWatchLogs, prevInputs, newInputs }) => ({
    type: CLOUDWATCH_LOGS_INPUT_BULK_EDIT,
    useCloudWatchLogs,
    prevInputs,
    newInputs,
    account,
  });



